<template>
  <div>
    <TitleBar
      title="阅读历史"
      rightText="清空"
      :clickRight="clearAllreadHistory"
    />
    <div style="margin-top: 10px"></div>
    <van-empty
      v-if="!historyList.length > 0"
      description="阅读历史记录是空的哦"
    />
    <van-row
      v-for="(item, index) in historyList"
      :key="index"
      @click="enterBookDetail(item.id)"
    >
      <van-row v-if="historyList[index].time" style="min-width: 209px">
        <van-col span="22" offset="1">
          <div class="parent" style="position: relative">
            <div class="div1" style="min-width: 116px; min-height: 102px">
              <img
                style="
                  width: 100%;
                  height: 100%;
                  min-width: 116px
                  min-height: 102px;
                "
                :src="item.cover"
                @error="errorImg"
                alt=""
              />
            </div>
            <div class="div2">{{ item.title }}</div>
            <div class="div3">{{ item.author }}</div>
            <div class="div4">最新:{{ item.updateTitle }}</div>
            <div class="div5">更新时间:{{ getDateTime(item.updateTime) }}</div>
            <div class="div6" style="margin: 0; position: absolute; bottom: 0">
              上一次阅读:{{ getDateTime(item.time) }}
            </div>
          </div>
        </van-col>
      </van-row>
      <van-divider />
    </van-row>
  </div>
</template>

<script>
import TitleBar from "../components/TitleBar.vue";
import { Dialog } from "vant";
export default {
  components: { TitleBar, [Dialog.Component.name]: Dialog.Component },
  data() {
    return {
      // 历史列表
      historyList: [],
    };
  },
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      var historyObj = JSON.parse(localStorage.getItem("localHistory")) || {};
      var tempHistoryList = [];
      Reflect.ownKeys(historyObj).forEach((i) => {
        // console.log(i, historyObj[i]);
        tempHistoryList.push(historyObj[i]);
      });
      tempHistoryList = vm.bubbleSort(tempHistoryList);

      // 获取到最新的阅读历史
      tempHistoryList.forEach((item) => {
        vm.historyList.push({ id: item.bookId, type: "book" });
      });
      vm.historyList = JSON.stringify(vm.historyList);

      // 请求历史记录的数据
      vm.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/shelf/detail.json?ids=${vm.historyList}`
        )
        .then((res) => {
          vm.historyList = res.data.data.list;
          console.log("请求到的:", vm.historyList);
          vm.historyList.forEach((item, index) => {
            item, index;
            item.time = tempHistoryList[index].time;
          });
        });
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    next((this.historyList = []));
  },
  methods: {
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 进入书籍详情
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    getDateTime(value) {
      // 时间戳转换为当前地区的标准时间
      let time = new Date(value);
      // 自带的etFullYear方法可以将标准时间返回一个表示年份的 4 位数字
      let year = time.getFullYear();
      //getMonth()方法可将标准时间返回表示月份的数字。返回值是0(一月)到11(十二月)之间的一个整数。
      let month = time.getMonth() + 1;
      //getDate()可返回标准时间的月份的某一天
      let date = time.getDate();
      // .getHours()可返回标准时间的小时字段。
      let hour = time.getHours();
      //
      let minute = time.getMinutes();
      // let second = time.getSeconds();
      // 获取当前标准时间 判断获得的时间是否小于当前年份
      let currentTime = new Date();
      let currentYear = currentTime.getFullYear();
      month = month < 10 ? "0" + month : month;
      date = date < 10 ? "0" + date : date;
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      if (year < currentYear) {
        let str =
          String(year) +
          "年" +
          String(month) +
          "月" +
          String(date) +
          "日" +
          " " +
          String(hour) +
          ":" +
          String(minute);

        return str;
      } else {
        let str =
          String(month) +
          "月" +
          String(date) +
          "日" +
          " " +
          String(hour) +
          ":" +
          String(minute);
        return str;
      }
    },
    // 冒泡排序
    bubbleSort(arr) {
      if (arr == null || arr.length < 2) {
        return arr;
      }
      //记录最后一次交换的位置
      var lastExchangeIndex = 0;
      //无序数列的边界，每次比较只需要比到这里为止
      var sortBorder = arr.length - 1;
      for (var i = 0; i < arr.length - 1; i++) {
        var isSorted = true; //有序标记，每一轮的初始是true
        for (var j = 0; j < sortBorder; j++) {
          if (arr[j + 1].time > arr[j].time) {
            isSorted = false; //有元素交换，所以不是有序，标记变为false
            var t = arr[j];
            arr[j] = arr[j + 1];
            arr[j + 1] = t;
            lastExchangeIndex = j;
          }
        }
        sortBorder = lastExchangeIndex;
        //一趟下来是否发生位置交换，如果没有交换直接跳出大循环
        if (isSorted) break;
      }
      return arr;
    },
    clearAllreadHistory() {
      Dialog.confirm({
        title: "清空阅读历史",
        message: "确定要清空所有的阅读历史记录吗？",
      })
        .then(() => {
          localStorage.removeItem("localHistory");
          this.historyList = [];
          this.$toast("清空成功");
        })
        .catch(() => {
          this.$toast("好的");
        });
    },
  },
};
</script>

<style>
[v-cloak] {
  display: none;
}
.parent {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(4, 2fr) 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  text-align: left;
}

.div1 {
  grid-area: 1 / 1 / 6 / 3;
  margin-right: 20px;
}
.div2 {
  grid-area: 1 / 3 / 2 / 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  line-height: 45.8px;
  font-size: 20px;
}
.div3 {
  grid-area: 2 / 3 / 3 / 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  line-height: 45.8px;
  color: gray;
}
.div4 {
  grid-area: 3 / 3 / 4 / 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  line-height: 45.8px;
  color: gray;
}
.div5 {
  grid-area: 4 / 3 / 5 / 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  line-height: 45.8px;
  color: gray;
}
.div6 {
  grid-area: 5 / 3 / 6 / 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  color: gray;
}
</style>