<template>
  <div class="row_three" v-if="row_three_book_sort">
    <!-- 推荐标题 -->
    <h1 class="title">
      {{ row_three_book_sort.name }}
    </h1>
    <div class="bottom">
      <ul>
        <li
          v-for="(item, index) in row_three_book_sort.list"
          :key="index"
          @click="enterBookDetail(item.id)"
        >
          <b :class="'icon_' + `${index + 1}`"></b>
          <img @error="errorImg" :src="item.cover + thumbnail" alt="书籍封面" />
          <h4>{{ item.title }}</h4>
        </li>
      </ul>
      <div
        v-if="row_three_book_sort.more"
        class="read_more"
        @click="enterChannelRank(row_three_book_sort.more.url)"
      >
        {{ row_three_book_sort.more.name }}
        <span class="iconfont icon-youjiantou"></span>
      </div>
    </div>
    <div class="underline"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      name: "",
    };
  },
  props: ["row_three_book_sort"],
  computed: {
    ...mapState(["thumbnail"]),
    // ...mapState(["row_three_book_sort"]),
  },
  watch: {
    row_three_book_sort(newVal, oldVal) {
      oldVal;
      this.row_three_book_sort = newVal;
    },
  },
  methods: {
    // 查看榜单详情
    enterChannelRank(url) {
      this.$router.push({
        name: "channelrank",
        query: {
          url: url,
        },
      });
    },
    // 进入书籍详情
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
  },
};
</script>

<style lang="less" scoped>
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
  margin-top: 10px;
}
.row_three {
  margin-left: 2vw;
  margin-right: 2vw;
  // background: blue;
  .title {
    margin-bottom: 20px;
    margin-top: 10px;
    text-align: left;
    font-weight: bold;
    font-size: 20px;
    color: #333;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    word-wrap: break-word;
  }
  .bottom {
    padding-bottom: 10px;

    ul {
      display: flex;
      justify-content: space-between;
      li {
        width: 28%;
        position: relative;
        b {
          position: absolute;
          top: -12px;
          left: 0px;
          right: 0;
          width: 100%;
          height: 40px;
          z-index: 9;
        }
        .icon_1 {
          background: url(../assets/img/icon_01.png) 0 0 no-repeat;

          background-size: 100% auto;
        }
        .icon_2 {
          background: url(../assets/img/icon_02.png) 0 0 no-repeat;

          background-size: 100% auto;
        }
        .icon_3 {
          background: url(../assets/img/icon_03.png) 0 0 no-repeat;

          background-size: 100% auto;
        }
        img {
          width: 100%;
        }
        h4 {
          text-align: left;
          font-size: 15px;
          line-height: 19px;
          height: 38px;
          max-height: 38px;
          margin-top: 10px;
          margin-bottom: 5px;
          overflow: hidden;
          color: #222;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
    }
  }
}
</style>