<template>
  <!-- 横向四书组件 -->
  <div class="row_three" v-if="row_four_book_sort">
    <!-- 推荐标题 -->
    <h1 class="title">
      {{ row_four_book_sort.name }}
    </h1>
    <div class="bottom">
      <ul>
        <li
          v-for="(item, index) in row_four_book_sort.list"
          :key="index"
          @click="enterBookContentOrDetail(item.id, row_four_book_sort.name)"
        >
          <img @error="errorImg" :src="item.cover + thumbnail" alt="书籍封面" />
          <h4>{{ item.title }}</h4>
          <span>{{ item.author }}</span>
        </li>
      </ul>
    </div>
    <div class="underline"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      name: "",
    };
  },
  props: ["row_four_book_sort"],
  computed: {
    ...mapState(["thumbnail"]),
    // ...mapState(["row_four_book_sort"]),
  },
  methods: {
    // 进入内容还是书详情界面
    enterBookContentOrDetail(bookId, name) {
      if (name == "新书独家") {
        this.$router.push({
          path: "/bookcontent",
          query: {
            id: bookId,
          },
        });
      } else if (name == "潜力IP") {
        this.$router.push({
          name: "bookdetail",
          query: {
            id: bookId,
          },
        });
      } else {
        this.$router.push({
          name: "bookdetail",
          query: {
            id: bookId,
          },
        });
      }
    },
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
  },
  watch: {
    row_four_book_sort(newVal, oldVal) {
      oldVal;
      this.row_four_book_sort = newVal;
    },
  },
};
</script>

<style lang="less" scoped>
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
  margin-top: 10px;
}
.row_three {
  margin-left: 2vw;
  margin-right: 2vw;
  // background: blue;
  .title {
    margin-bottom: 20px;
    margin-top: 10px;
    text-align: left;
    font-weight: bold;
    font-size: 20px;
    color: #333;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    word-wrap: break-word;
  }
  .bottom {
    padding-bottom: 10px;

    ul {
      display: flex;
      justify-content: space-between;
      li {
        width: 20%;

        img {
          width: 100%;
        }
        h4 {
          text-align: left;
          font-size: 15px;
          line-height: 19px;
          height: 38px;
          max-height: 38px;
          margin-top: 10px;
          margin-bottom: 5px;
          overflow: hidden;
          color: #222;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        span {
          text-align: left;
          font-size: 12px;
          color: #888888;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
}
</style>