<template>
  <!-- 频道排行榜页面 -->
  <div style="position: relative">
    <van-row>
      <!-- 返回 -->
      <van-col span="4" offset="1">
        <p style="height: 44px; line-height: 44px" @click="$router.back()">
          <span>
            <i
              style="font-size: 20px"
              class="iconfont icon-xiangzuojiantou"
            ></i>
          </span>
        </p>
      </van-col>
      <van-col span="17" offset="1">
        <van-tabs v-model="active" :swipeable="true" @change="changeChannel">
          <!-- 频道分类导航 -->
          <van-tab
            v-for="(channel, index) in channelList"
            :key="index"
            :title="channel.name"
          >
            <van-sidebar
              style="position: absolute; top: 44px; left: -93px"
              v-model="activeKey"
            >
              <!-- 左侧排行榜 -->
              <van-sidebar-item
                @click="changeRank(i)"
                v-for="(rank, i) in channel.subNavis"
                :key="i"
                style="background-color: #fff"
                :title="rank.name"
              />
            </van-sidebar>
            <!-- 空状态 -->
            <van-empty v-if="!bookList.length > 0" description="加载中..." />
            <van-list
              v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="onLoad"
              :error.sync="error"
              error-text="请求失败，点击重新加载"
              :immediate-check="false"
            >
              <!-- 渲染出来的书籍列表 -->
              <div
                style="
                  text-align: left;
                  display: flex;
                  margin-top: 10px;
                  margin-bottom: 10px;
                "
                v-for="(book, b) in bookList"
                :key="b"
                @click="enterBookDetail(book.id)"
              >
                <div style="widht: 70px; height: 100px; min-width: 70px">
                  <img
                    style="width: 70px; height: 100px; min-width: 70px"
                    :src="book.cover + '?imageView&thumbnail=240y336'"
                    alt=""
                    @error="errorImg"
                  />
                </div>
                <div
                  style="padding-left: 10px; position: relative; width: 100%"
                >
                  <p
                    style="
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      position: relative;
                      font-size: 18px;
                      width: 70%;
                    "
                  >
                    {{ book.title }}
                  </p>
                  <span
                    v-if="b < 3"
                    style="position: absolute; top: 0; right: 0px"
                    >TOP{{ b + 1 }}</span
                  >
                  <span
                    v-else
                    style="position: absolute; top: 0; right: 0; color: gray"
                  >
                    {{ b + 1 }}
                  </span>
                  <p
                    style="
                      font-size: 14px;
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      color: gray;
                      padding-top: 5px;
                      word-break: break-all;
                    "
                  >
                    {{ book.content }}
                  </p>
                  <p
                    style="
                      font-size: 14px;
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      position: absolute;
                      bottom: 0px;
                      color: gray;
                    "
                  >
                    {{ book.author }} | {{ book.category }}
                  </p>
                  <span
                    v-if="book.clickCount > -1"
                    style="position: absolute; bottom: 0; right: 0; color: red"
                  >
                    {{ changeWan(book.clickCount) }}人气</span
                  >
                </div>
              </div>
            </van-list>
          </van-tab>
        </van-tabs>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 控制频道分类的出现位置
      active: 0,
      // 控制排行榜的出现位置
      activeKey: 1,
      channelList: [],
      rankList: [],
      resultList: [],
      bookList: [],
      // 滚动加载
      loading: false,
      finished: false,
      error: false,
      moreUrl: null,
    };
  },

  // 进入该路由之前
  beforeRouteEnter(to, from, next) {
    to, from;
    console.log(to.query.url);
    console.log(from.query.url);
    next((vm) => {
      // 如果来者不是首页, 则不需要清空原来的数据
      if (from.path.indexOf("/bookmall") !== -1) {
        // 获取分类的总数据
        window.scrollTo(0, 0);
        vm.axios
          .get(`https://apis.netstart.cn/yunyuedu${to.query.url}`)
          .then((res) => {
            vm.$nextTick(() => {
              // 拿到分类的数据
              vm.channelList = res.data.data.list;
              vm.channelList.forEach((item, index) => {
                if (item.select === true) {
                  vm.active = index;
                }
              });
              vm.rankList = res.data.data.list[vm.active].subNavis;
              vm.rankList.forEach((element, index) => {
                if (element.select === true) {
                  vm.activeKey = index;
                  vm.resultList = element;
                }
              });
              vm.axios
                .get(
                  `https://apis.netstart.cn/yunyuedu${vm.resultList.naviInfos[0].url}`
                )
                .then((res) => {
                  vm.$nextTick(() => {
                    console.log("res.data.list===>", res.data.list);
                    vm.bookList = res.data.list;
                    console.log("vm.bookList===>", vm.bookList);
                    vm.moreUrl = res.data.more.url;
                  });
                });
            });
          });
      }
    });
  },
  methods: {
    // 进入书籍详情
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 切换频道
    changeChannel(name, title) {
      name, title;
      this.bookList = [];
      this.rankList = this.channelList[name].subNavis;
      this.activeKey = 0;
      this.rankList[0];
      this.resultList = this.rankList[0];
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu${this.resultList.naviInfos[0].url}`
        )
        .then((res) => {
          this.$nextTick(() => {
            this.bookList = res.data.list;
            this.moreUrl = res.data.more.url;
            this.loading = false;
            this.finished = false;
          });
        });
    },
    //////////////////////////
    //////////////////////
    /////////////////////
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },

    // 转换为万
    changeWan(num) {
      if (num > 10000) {
        return (num / 10000).toFixed(1) + "万";
      } else {
        return num;
      }
    },
    onLoad() {
      this.axios
        .get(`https://apis.netstart.cn/yunyuedu${this.moreUrl}`)
        .then((res) => {
          this.$nextTick(() => {
            this.bookList.push(...res.data.list);
            this.moreUrl = res.data.more.url;
          });
          this.loading = false;
          if (res.data.more.url === undefined) {
            this.finished = true;
          }
        })
        .catch((err) => {
          err;
          this.error = true;
          this.loading = false;
        });
    },
    // 点击切换左侧的排行榜
    changeRank(index) {
      if (this.activeKey === index) {
        return;
      } else {
        this.bookList = [];
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu${this.rankList[index].naviInfos[0].url}`
          )
          .then((res) => {
            this.$nextTick(() => {
              this.bookList = res.data.list;
              this.moreUrl = res.data.more.url;
              this.loading = false;
              this.finished = false;
            });
          });
      }
    },
  },
};
</script>

