<template>
  <div>
    <!-- 四个频道 -->
    <!-- tabs -->
    <div style="margin: 0 5%; position: relative" class="container">
      <div
        style="position: absolute; top: 7px; right: 32px"
        @click="EnterSearch"
      >
        <span>
          <i style="font-size: 24px" class="iconfont icon-sousuo"></i>
        </span>
      </div>
      <van-tabs
        v-model="channelActive"
        class="channelList"
        title-active-color="black"
        color="#fff"
        @click="passSession"
      >
        <!-- 男生 -->
        <van-tab
          :title="channelNameList[0]"
          class="male"
          :title-style="
            channelActive == 0
              ? 'font-size:20px;font-weight:bold;'
              : 'font-size:14px'
          "
        >
          <!-- 男生合集 -->

          <van-tabs
            @click="passMaleUrl"
            v-if="result.length > 0"
            v-model="maleCateActive"
            class="cate"
            title-active-color="red"
            style="min-height: 100%; text-align: left"
          >
            <!-- 都市 -->
            <van-tab
              v-for="cate in result[0].cates"
              :key="cate.url"
              :title="cate.name"
              route
              :to="
                '/classify/male' +
                trimAll(pinying(cate.name, { toneType: 'none' }))
              "
            >
            </van-tab>
          </van-tabs>
        </van-tab>
        <!-- 女生 -->
        <van-tab
          :title="channelNameList[1]"
          class="male"
          :title-style="
            channelActive == 1
              ? 'font-size:20px;font-weight:bold;'
              : 'font-size:14px'
          "
        >
          <!-- 女生合集 -->
          <van-tabs
            @click="passFemaleUrl"
            v-if="result.length > 0"
            v-model="femaleCateActive"
            class="cate"
            title-active-color="red"
            style="min-height: 100%; text-align: left"
          >
            <!-- 女生都市 -->
            <van-tab
              v-for="cate in result[1].cates"
              :key="cate.url"
              :title="cate.name"
              route
              :to="
                '/classify/female' +
                trimAll(pinying(cate.name, { toneType: 'none' }))
              "
            >
            </van-tab>
          </van-tabs>
        </van-tab>
        <!--出版 -->
        <van-tab
          :title="channelNameList[2]"
          class="male"
          :title-style="
            channelActive == 2
              ? 'font-size:20px;font-weight:bold;'
              : 'font-size:14px'
          "
        >
          <!--出版合集 -->
          <van-tabs
            @click="passPublishUrl"
            v-if="result.length > 0"
            v-model="publishCateActive"
            class="cate"
            title-active-color="red"
            style="min-height: 100%; text-align: left"
          >
            <!-- 出版都市 -->
            <van-tab
              v-for="cate in result[2].cates"
              :key="cate.url"
              :title="cate.name"
              route
              :to="
                '/classify/publish' +
                trimAll(pinying(cate.name, { toneType: 'none' }))
              "
            >
            </van-tab>
          </van-tabs>
        </van-tab>
        <van-tab
          :title="channelNameList[3]"
          class="male"
          :title-style="
            channelActive == 3
              ? 'font-size:20px;font-weight:bold;'
              : 'font-size:14px'
          "
        >
          <van-tabs
            @click="passListenUrl"
            v-if="result.length > 0"
            v-model="listenCateActive"
            class="cate"
            title-active-color="red"
            style="min-height: 100%; text-align: left"
          >
            <!-- 都市 -->
            <van-tab
              v-for="cate in result[3].cates"
              :key="cate.url"
              :title="cate.name"
              route
              :to="
                '/classify/listen' +
                trimAll(pinying(cate.name, { toneType: 'none' }))
              "
            >
            </van-tab>
          </van-tabs>
        </van-tab>
      </van-tabs>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import { pinyin } from "pinyin-pro";
export default {
  data() {
    return {
      channelActive: 0,
      maleCateActive: 0,
      femaleCateActive: 0,
      publishCateActive: 0,
      listenCateActive: 0,
      channelNameList: [],
      // 各个频道的分类合集
      catesList: [],
      result: [],
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/yunyuedu/mergedCategroy.json")
      .then((res) => {
        this.$nextTick(() => {
          this.result = res.data;
          res.data.forEach((element) => {
            this.channelNameList.push(element.name);
          });
          // this.catesList = res.data[this.channelActive].cates;
        });
      });
  },

  methods: {
    // 进入搜索栏
    EnterSearch() {
      this.$router.push({
        name: "searchHint",
      });
    },
    // 传递男生页面时所选择的分类ID
    passMaleUrl(name, title) {
      console.log(name, title);
      sessionStorage.setItem(
        "catepath",
        "/classify/male" +
          this.trimAll(this.pinying(title, { toneType: "none" }))
      );
      let maleUrl = this.result[0].cates[name].url;
      console.log(this.getStrAfter(maleUrl, "catId="));
      // 点击分类后传递给vuex
      this.$store.dispatch("getMaleUrl", this.getStrAfter(maleUrl, "catId="));
    },
    // 传递女生页面时所选择的分类ID
    passFemaleUrl(name, title) {
      console.log(name, title);
      sessionStorage.setItem(
        "catepath",
        "/classify/female" +
          this.trimAll(this.pinying(title, { toneType: "none" }))
      );
      let femaleUrl = this.result[1].cates[name].url;
      console.log(this.getStrAfter(femaleUrl, "catId="));
      // 点击分类后传递给vuex
      this.$store.dispatch(
        "getFemaleUrl",
        this.getStrAfter(femaleUrl, "catId=")
      );
    },
    // 传递出版页面时所选择的分类ID
    passPublishUrl(name, title) {
      console.log(name, title);
      sessionStorage.setItem(
        "catepath",
        "/classify/publish" +
          this.trimAll(this.pinying(title, { toneType: "none" }))
      );
      let publishUrl = this.result[2].cates[name].url;
      console.log(this.getStrAfter(publishUrl, "catId="));
      // 点击分类后传递给vuex
      this.$store.dispatch(
        "getPublishUrl",
        this.getStrAfter(publishUrl, "catId=")
      );
    },
    // 传递听书页面时所选择的分类ID
    passListenUrl(name, title) {
      console.log(name, title);
      sessionStorage.setItem(
        "catepath",
        "/classify/listen" +
          this.trimAll(this.pinying(title, { toneType: "none" }))
      );
      let listenUrl = this.result[3].cates[name].url;
      console.log(this.getStrAfter(listenUrl, "catId="));
      // 点击分类后传递给vuex
      this.$store.dispatch(
        "getListenUrl",
        this.getStrAfter(listenUrl, "catId=")
      );
    },
    // 获取某个字符串后面的所有字符
    getStrAfter(str, afterStr) {
      return str.split(afterStr)[1];
    },
    // 汉字转拼音
    pinying(str, options) {
      return pinyin(str, options);
    },
    // 去除字符串空格
    trimAll(str) {
      return str.replace(/\s+/g, "");
    },
    // 传递频道的storage值
    passSession(name) {
      sessionStorage.setItem("channelId", name);
    },
  },
  mounted() {
    console.log(pinyin("亲子儿童"));
  },
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      if (sessionStorage.getItem("catepath")) {
        // vm.channelActive = sessionStorage.getItem("channelId");
        vm.$router.push({ path: sessionStorage.getItem("catepath") });
      } else {
        //
      }
    });
  },
};
</script>
<style lang="less" >
.channelList {
  width: 60vw;
  min-width: 225px;
  flex-wrap: no-wrap !important;
  justify-content: space-between;

  .male {
    .van-tabs {
      width: 94vw;
      flex-wrap: wrap !important;
      justify-content: space-between;
    }
    .van-tabs--line .van-tabs__wrap {
      height: auto !important;
    }
  }
  .van-tabs__line {
    display: none;
  }

  .van-tabs__nav {
    flex-wrap: wrap !important;
  }
  .van-tabs__nav--line.van-tabs__nav--complete {
    padding-right: 0px !important;
    padding-left: 10px !important;
  }
  .van-tabs__wrap--scrollable .van-tab {
    flex: 0 1 auto !important;
    padding: 0 !important;
    margin-right: 10px !important;
  }
}
</style>