<template>
  <div v-if="five_book_sort">
    <div
      v-if="five_book_sort.list"
      class="top"
      @click="enterBokkDetail(five_book_sort.list[0].id)"
    >
      <div class="left">
        <!-- 本周强推 -->
        <h3>{{ five_book_sort.name }}</h3>
        <!-- 书籍封面 -->
        <div class="img_div">
          <img
            :src="five_book_sort.list[0].cover + thumbnail"
            alt="书籍封面"
            @error="errorImg"
          />
        </div>
      </div>
      <div class="right">
        <!-- 书名 -->
        <h1>{{ five_book_sort.list[0].title }}</h1>
        <!-- 作者/分类 -->
        <h5>
          {{ five_book_sort.list[0].author }}
          <span v-if="five_book_sort.list[0].category">/</span>
          {{ five_book_sort.list[0].category }}
        </h5>
        <!-- 小说简介 -->
        <h5 style="word-break: break-all">
          {{ five_book_sort.list[0].content }}
        </h5>
      </div>
    </div>

    <div v-if="five_book_sort.list" class="bottom">
      <ul>
        <li
          v-for="(item, index) in filtersList(five_book_sort.list)"
          :key="index"
          @click="enterBokkDetail(item.id)"
        >
          <div>
            <img :src="item.cover + thumbnail" alt="" />
          </div>
          <p
            style="
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
              word-break: break-all;
            "
          >
            {{ item.title }}
          </p>
          <span>{{ item.author }}</span>
        </li>
      </ul>
    </div>
    <!-- <button @click="shuchu">111</button> -->
    <!-- 非常浅的下划线 -->
    <div class="underline"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  props: ["five_book_sort"],

  computed: {
    ...mapState(["thumbnail"]),
    // ...mapState(["five_book_sort"]),
  },

  watch: {
    five_book_sort(newVal, oldVal) {
      oldVal;
      this.five_book_sort = newVal;
    },
  },

  methods: {
    // 进入书籍详情
    enterBokkDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 过滤获取除了第一本书的剩下书籍
    filtersList(value) {
      let newList;
      newList = value.filter((item, index) => {
        return index > 0;
      });
      return newList;
    },
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
  },
};
</script>

<style lang="less" scoped>
.top {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
  margin-bottom: 10px;
  .left {
    text-align: left;
    margin-left: 2.5vw;
    h3 {
      display: inline-block;
      font-size: 20px;
      color: #1a1a1a;
      line-height: 28px;
      font-weight: bold;
      padding-bottom: 10px;
    }
    .img_div {
      width: 25vw;
    }
  }
  .right {
    text-align: left;
    margin-top: 34px;
    margin-left: 15px;
    h1 {
      font-size: 20px;
      color: #1a1a1a;
      font-weight: bold;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      margin-bottom: 10px;
    }
    h5:nth-of-type(1) {
      font-size: 14px;
      color: gray;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      margin-bottom: 25px;
    }
    h5:nth-of-type(2) {
      font-size: 14px;
      color: gray;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
    }
  }
}
.bottom {
  margin-bottom: 20px;
  ul {
    display: flex;
    justify-content: space-between;
    margin-left: 2.5vw;
    width: 100%;
    li {
      width: 20%;
      text-align: left;
      margin-right: 5vw;
      div {
        img {
          width: 25vw;
          height: 27vw;
        }
      }
      p {
        font-size: 15px;
      }
      span {
        font-size: 12px;
        color: #888888;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        word-break: break-all;
      }
    }
  }
}
// 建立一个div,生成非常非常浅的下划线,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
