<template>
  <div @scroll="loadMore($event)" class="BookCatalogView">
    <!--书籍封面图片 -->
    <div v-if="bookInfo" class="top">
      <div class="left_img">
        <img
          :src="bookInfo.coverImage + thumbnail"
          alt="小说封面"
          @error="errorImg"
        />
      </div>
      <div class="right_txt">
        <div class="frame">
          <p>{{ bookInfo.title }}</p>
          <span>{{ bookInfo.author }}</span>
        </div>
      </div>
    </div>
    <!-- 章节数量 -->
    <div
      style="
        padding-left: 5%;
        text-align: left;
        margin-left: 10px;
        margin-bottom: 20px;
      "
    >
      共{{ bookCatalog.length }}章
    </div>
    <div class="catalog">
      <ul>
        <!-- 遍历章节目录 -->
        <li
          v-for="(chapter, index) in bookCatalog.slice(startNum, endNum)"
          :key="chapter.secId"
          ref="chapter"
          @click="sendChapterIndex_secId(index, chapter.secId, chapter.needPay)"
        >
          <h2>
            <p>
              {{ chapter.title }}
              <span v-if="chapter.needPay" class="iconfont icon-suotou"></span>
            </p>
          </h2>
          <div class="dashline"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      // 书籍目录数组
      bookCatalog: [],
      // 书籍相关信息
      bookInfo: null,
      startNum: 0,
      endNum: 100,
      newList: [],
    };
  },
  props: ["highLighIndex"],
  watch: {
    // 利用watch监听结束值的变化,一旦有所改变,就重新发送请求
    endNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);
      // 自定义加载图标
      this.$toast.loading({
        message: "加载目录中...",
        forbidClick: true,
        loadingType: "circular",
        duration: 200,
      });
    },
    // 接受索引并监控他的变化,一旦有所改变,就重新改变目录高亮位置(就清除所有高亮,唯独索引位置高亮)
    highLighIndex: function (newVal, oldVal) {
      oldVal;
      this.highLighIndex = newVal;
      // 更新高亮位置
      this.$nextTick(() => {
        this.updateHighLigth();
      });
    },
  },
  computed: {
    ...mapState(["source_uuid"]),
    ...mapState(["thumbnail"]),
  },

  updated() {
    this.$nextTick(() => {
      // 更新高亮
      this.updateHighLigth();
    });
  },
  created() {
    // 自定义加载图标
    this.$toast.loading({
      message: "加载目录中...",
      forbidClick: true,
      loadingType: "circular",
      duration: 0,
    });
    // 获取到书籍章节列表
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
      )
      .then((res) => {
        // 书籍目录赋予bookCatalog
        this.bookCatalog = res.data.data.catalog;
        this.bookInfo = res.data.data.book;
        this.$toast.clear();
      });
  },
  // 激活
  activated() {
    if (this.source_uuid != this.$route.query.id) {
      console.log(
        "目录(或者说内容)页面被激活了,但是this.source_uuid != this.$route.query.id"
      );
      // 自定义加载图标
      this.$toast.loading({
        message: "正在加载新的目录...",
        forbidClick: true,
        loadingType: "circular",
        duration: 0,
      });
      // 获取到书籍章节列表
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
        )
        .then((res) => {
          // 书籍目录赋予bookCatalog
          this.bookCatalog = res.data.data.catalog;
          this.bookInfo = res.data.data.book;
          this.$toast.clear();
        });
    }
  },
  // 失活时
  deactivated() {},
  mounted() {
    // for (let i = 0; i < this.$refs.chapter.length; i++) {
    //   this.$refs.chapter[i].classList.remove("highLight");
    // }
    // // 点击当前章节显示高亮
    // this.$refs.chapter[this.highLighIndex].classList.add("highLight");
  },
  methods: {
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 更新高亮的方法
    updateHighLigth() {
      // 更新高亮位置
      if (this.$refs.chapter) {
        for (let i = 0; i < this.$refs.chapter.length; i++) {
          this.$refs.chapter[i].classList.remove("highLight");
        }
        // 当前章节显示高亮
        this.$refs.chapter[this.highLighIndex].classList.add("highLight");
      }
    },
    // 点击章节传递索引并高亮
    sendChapterIndex_secId(index, secId, needPay) {
      console.log(
        "你点击的这个章节的索引是:",
        index,
        "这个章节的secId是:",
        secId
      );
      // 触发自定义事件:将ID和index,needPay传送给内容页
      this.$emit("clickChapter", index, secId, needPay);
      for (let i = 0; i < this.$refs.chapter.length; i++) {
        this.$refs.chapter[i].classList.remove("highLight");
      }
      // 点击当前章节显示高亮
      this.$refs.chapter[index].classList.add("highLight");
    },

    // 加载更多
    loadMore(e) {
      // e.srcElement.scrollTop 滚动了的距离
      // e.srcElement.offsetHeight 固定不动着能一眼看到的界面高度
      // e.srcElement.scrollHeight 总共可以给你滚动的距离,但由于有个界面在那里杵着,所以只能说是理想可滚动距离,实际滚动了的距离是scrollHeight-offsetHeight
      /* console.log("e.srcElement.scrollTop", e.srcElement.scrollTop);
      console.log("e.srcElement.offsetHeight", e.srcElement.offsetHeight);
      console.log("e.srcElement.scrollHeight", e.srcElement.scrollHeight);*/

      if (
        e.srcElement.scrollTop + e.srcElement.offsetHeight >=
        e.srcElement.scrollHeight
      ) {
        if (this.endNum < this.bookCatalog.length) {
          this.endNum = this.endNum + 250;
        } else {
          this.$toast({
            message: "已经加载全部章节!",
            position: "bottom",
          });
        }
        // this.newList = this.bookCatalog.slice(this.startNum, this.endNum);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.BookCatalogView {
  height: 100vh;
  overflow: scroll;
  .top {
    display: flex;
    margin-left: 5%;
    margin-top: 7%;
    .left_img {
      img {
      }
    }
    .right_txt {
      // background: red;
      // padding-top: 40px;
      margin-left: 5%;
      width: 100%;
      text-align: left;
      .frame {
        margin-top: 20px;
        // background: red;
        p {
          font-size: 20px;
          margin-bottom: 10px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
        span {
          color: gray;
          font-size: 16px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .catalog {
    // height: 100%;
    // overflow: auto;
    ul {
      text-align: left;
      padding: 0 10px;
      li {
        padding-left: 5%;
        h2 {
          margin-bottom: 5px;
          p {
            position: relative;
            span {
              position: absolute;
              right: 20px;
            }
          }
        }

        .dashline {
          border: 1px gray dashed;
          margin-bottom: 5px;
        }
      }
    }
  }
  .highLight {
    color: red;
  }
}
</style>