<template>
  <!-- 返回 标题  -->
  <div>
    <!-- :title="$route.query.title ? $route.query.title : title || text" -->
    <van-nav-bar
      v-if="$route"
      :title="
        realTitle && realCount ? realTitle + '(' + realCount + ')' : realTitle
      "
      :left-text="leftText ? leftText : ''"
      :right-text="rightText ? rightText : ''"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      realCount: "",
      realTitle: "",
      realLeftText: "",
      realRightText: "",
    };
  },
  props: ["title", "totalCount", "leftText", "rightText", "clickRight"],

  created() {
    // r如果有标题传过来
    if (this.title) {
      console.log("有title传过来");
      this.realTitle = this.title;
    }
    // 如果有左侧标题传过来
    if (this.leftText) {
      console.log("有左侧标题传过来");
      this.realLeftText = this.leftText;
    }
    // r如果有右侧标题传过来
    if (this.rightText) {
      console.log("有右侧标题传过来");
      this.realRightText = this.rightText;
    }
  },
  methods: {
    onClickRight() {
      if (this.clickRight) {
        this.clickRight();
      }
    },
    onClickLeft() {
      history.back();
    },
    getComment() {
      this.axios.get(
        "https://apis.netstart.cn/yunyuedu/comment/getComments.json?bookId=${this.$route.query.id}&page=1"
      );
    },
  },
  mounted() {},
  watch: {
    totalCount(newVal, oldVal) {
      oldVal;
      this.realCount = newVal;
      console.log("有totalCount传过来", this.totalCount);
      console.log("this.realCount是", this.realCount);
    },
    title(newVal, oldVal) {
      oldVal;
      this.realTitle = newVal;
    },
  },
};
</script>

