
<template>
  <div
    class="comment_view"
    v-if="commentResult"
    @scroll="loadMore($event)"
    style="height: 100vh; overflow: scroll"
  >
    <div v-if="commentResult.data.all.list.length == 0">
      <p style="color: gray; margin-top: 20px">快来当第一个写评论的人吧!</p>
    </div>

    <van-row
      style="padding-top: 15px"
      v-for="(item, index) in commentResult.data.all.list"
      :key="index"
    >
      <van-skeleton
        title
        avatar
        animate
        avatar-size="50"
        :row="3"
        :loading="skeletonShow"
        row-width="90%"
      >
        <van-col offset="1" span="4">
          <!-- 头像 -->
          <div
            class="avatar"
            @click="
              enterUserInfoView(
                item.userId,
                item.userLevel,
                item.avatar,
                item.nickName
              )
            "
          >
            <img
              :src="
                item.avatar
                  ? item.avatar
                  : require('@/assets/img/readyAvator.webp')
              "
              alt=""
            />
          </div>
        </van-col>
        <van-col span="17" offset="1">
          <div class="right">
            <!-- 昵称与等级与点赞数 -->
            <div class="right_one">
              <p class="nick_name">
                <span
                  class="nickname"
                  @click="
                    enterUserInfoView(
                      item.userId,
                      item.userLevel,
                      item.avatar,
                      item.nickName
                    )
                  "
                  >{{ item.nickName }}</span
                >
                <span
                  :class="['level', 'iconfont', 'icon-level' + item.userLevel]"
                ></span>
              </p>
              <p class="likes">
                <span class="likes_num">{{ item.likes }}</span>
                <span class="iconfont icon-dianzan"> </span>
              </p>
            </div>
            <!-- 评星与时间 -->
            <div class="right_two">
              <p v-if="item.grade" class="rate">
                <van-rate
                  v-model="item.grade"
                  :size="14"
                  color="#ffd21e"
                  void-icon="star"
                  void-color="#eee"
                  readonly
                />
              </p>
              <p class="time">{{ getDateTime(item.time) }}</p>
            </div>
            <!-- 读者评论 -->
            <div class="right_three" @click="giveIndex(index)">
              <p class="comment">{{ item.comment }}</p>
            </div>
            <!-- 回复区域 -->
            <div class="right_four">
              <div class="comment">
                <p
                  v-if="item.replyList"
                  @click="giveReplyCommentNickName(item.replyList[0].nickName)"
                >
                  <span class="nickName">{{ item.replyList[0].nickName }}</span>
                  <span
                    v-if="item.replyList[0].self == 1"
                    class="author"
                    style="color: rgb(244 124 24); font-size: 14px"
                    >(作者)
                  </span>
                  回复
                  <span class="toNickName">{{
                    item.replyList[0].toNickName
                  }}</span>
                  :
                  <span>{{ item.replyList[0].comment }}</span>
                </p>
              </div>
              <div v-if="item.replyList" class="comment">
                <p
                  v-if="item.replyList[1]"
                  @click="giveReplyCommentNickName(item.replyList[1].nickName)"
                >
                  <span class="nickName">{{ item.replyList[1].nickName }}</span>
                  <span
                    v-if="item.replyList[1].self == 1"
                    class="author"
                    style="color: rgb(244 124 24); font-size: 14px"
                    >(作者)
                  </span>
                  回复
                  <span class="toNickName">{{
                    item.replyList[1].toNickName
                  }}</span>
                  :
                  <span>{{ item.replyList[1].comment }}</span>
                </p>
              </div>
              <!-- 选择是否显示剩余回复区域 -->
              <van-cell
                v-if="item.replyCount > 2"
                class="showAll"
                :ref="'showAll' + index"
                @click="
                  getAllcomment(
                    'https://apis.netstart.cn/yunyuedu' + item.replyUrl,
                    index
                  )
                "
              >
                <p>展开剩余{{ item.replyCount - 2 }}回复</p>
              </van-cell>
              <!-- 展示所有余下评论的弹窗 -->
              <van-popup
                class="allComment"
                v-model="allCommentShow"
                :overlay="false"
                position="bottom"
                :style="{ height: '100%' }"
              >
                <van-row>
                  <van-col
                    offset="1"
                    span="21"
                    style="
                      position: relative;
                      background: #e8e0e0;
                      height: 95vh;
                    "
                  >
                    <span
                      class="turnOff iconfont icon-guanbi"
                      style="
                        position: absolute;
                        top: -19px;
                        right: -22px;
                        font-size: 26px;
                      "
                      @click="closeAllPopup()"
                    ></span>
                    <div
                      :ref="'allComment' + index"
                      v-for="(listItem, listItemindex) in allList"
                      :key="listItemindex"
                    >
                      <p
                        @click="giveReplyAllCommentNickName(listItem.nickName)"
                      >
                        <span>{{ listItem.nickName }}</span
                        >回复<span>{{ listItem.toNickName }}</span
                        >:
                        <span>{{ listItem.comment }}</span>
                      </p>
                    </div>
                  </van-col>
                </van-row>
              </van-popup>
            </div>
            <div></div>
          </div>
        </van-col>
      </van-skeleton>
    </van-row>

    <div class="bottom_trim" style="margin-top: 50px"></div>
    <!--回复评论的弹窗 -->
    <van-popup
      @click-overlay="overlayClose()"
      position="bottom"
      :style="{ height: '166px' }"
      v-model="getCommentKey"
    >
      <div class="commentPopup">
        <textarea
          autofocus="autofocus"
          v-model="commentValue"
          :placeholder="'回复 ' + commentNickName + ':'"
          cols="20"
          rows="5"
        >
        </textarea>
        <div class="numSend">
          <p class="num">{{ commentValue.length }}字</p>
          <van-button class="send" size="small" color="#e64d2e"
            >发送</van-button
          >
        </div>
      </div>
    </van-popup>
    <!--楼中楼回复的弹窗 -->
    <van-popup
      @click-overlay="overlayClose()"
      position="bottom"
      :style="{ height: '166px' }"
      v-model="getReplyCommentKey"
    >
      <div class="replyCommentPopup">
        <textarea
          autofocus="autofocus"
          v-model="replyCommentValue"
          :placeholder="'回复 ' + replyCommentNickName + ':'"
          cols="20"
          rows="5"
        >
        </textarea>
        <div class="numSend">
          <p class="num">{{ replyCommentValue.length }}字</p>
          <van-button class="send" size="small" color="#e64d2e"
            >发送</van-button
          >
        </div>
      </div>
    </van-popup>
    <!--余下回复评论的弹窗 -->
    <van-popup
      @click-overlay="overlayClose()"
      position="bottom"
      :overlay="false"
      :style="{ height: '166px' }"
      v-model="getReplyAllCommentKey"
    >
      <div class="replyAllCommentPopup">
        <textarea
          autofocus="autofocus"
          v-model="replyAllCommentValue"
          :placeholder="'回复 ' + replyAllCommentNickName + ':'"
          cols="20"
          rows="5"
        >
        </textarea>
        <div class="numSend">
          <p class="num">{{ replyAllCommentValue.length }}字</p>
          <van-button class="send" size="small" color="#e64d2e"
            >发送</van-button
          >
        </div>
      </div>
    </van-popup>
    <!-- 最底下的评论编辑栏 -->
    <div
      v-if="!publishCommentShowKey"
      @click="publishCommentShow()"
      class="publishComment"
    >
      <van-col
        offset="1"
        span="22"
        style="height: 34px; margin-top: 8px; position: relative"
      >
        <div>
          <input
            type="text"
            name=""
            placeholder="写长评当大师,接受众人膝盖～"
          />
          <span class="editorComment iconfont icon-bianjishuru-xianxing"></span>
        </div>
      </van-col>
    </div>
    <!-- 评论编辑栏的弹窗 publishCommentPopup -->
    <van-popup
      @click-overlay="overlayClose()"
      position="bottom"
      :style="{ height: '200px' }"
      v-model="publishCommentShowKey"
    >
      <div style="display: flex; margin-top: 20px">
        <p style="font-size: 14px; padding-left: 20px">评分:</p>
        <van-rate
          v-model="rateCommentValue"
          touchable
          :size="22"
          color="#ffd21e"
          void-icon="star"
          void-color="#eee"
        />
      </div>
      <div class="publishCommentPopup">
        <textarea
          v-model="publishCommmentValue"
          placeholder="写长评当大师,接受众人膝盖"
          cols="20"
          rows="5"
        >
        </textarea>
        <div class="numSend">
          <p class="num">{{ publishCommmentValue.length }}字</p>
          <van-button class="send" size="small" color="#e64d2e"
            >发送</van-button
          >
        </div>
      </div>
    </van-popup>
  </div>
</template>

<style lang="less" scoped>
