            color: #a0a19e;
          }
          .toNickName {
            color: #a0a19e;
          }
        }
      }
      .showAll {
        background: #f5f5f5;
        p {
          text-align: center;
          color: #69b0ee;
        }
      }
      .allComment {
        background: #f5f5f5;

        div {
          margin-top: 1.55vh;

          p {
            background: #ddeaf5;
