    }
    .numSend {
      height: 20%;
      margin-left: 20px;
      width: calc(100% - 40px);
      display: flex;
      justify-content: space-between;
      .num {
        // display: block;
        color: gray;
        line-height: 37px;
      }
      .send {
        // display: block;
        width: 60px;
        margin-bottom: 5px;
      }
    }
  }
  // 回复评论弹窗
  .commentPopup {
    textarea {
      width: 100%;
      padding-left: 20px;
      padding-top: 15px;
      padding-right: 20px;
      border: none;
      height: 80%;
    }
    .numSend {
      height: 20%;
      margin-left: 20px;
      width: calc(100% - 40px);
      display: flex;
      justify-content: space-between;
      .num {
        // display: block;
        color: gray;
        line-height: 37px;
      }
