<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      skeletonShow: true,
      // 评论时先默认五星好评
      rateCommentValue: 5,
      // 要回复余下回复的它的昵称
      replyAllCommentNickName: "",
      // 回复余下回复的弹窗输入值
      replyAllCommentValue: "",
      // 控制回复余下回复的弹窗
      getReplyAllCommentKey: false,

      // 要回复楼中楼的它的昵称
      replyCommentNickName: "",
      // 回复楼中楼的弹窗输入值
      replyCommentValue: "",

      // 控制楼中楼评论的弹窗
      getReplyCommentKey: false,

      // 要回复评论的它的昵称
      commentNickName: "",
      // 回复评论的弹窗输入值
      commentValue: "",
      // 判断是回复第几条评论的关键值
      getCommentValue: null,
      // 控制回复评论的弹窗
      getCommentKey: false,
      // 最底下评论编辑栏的弹窗输入值
      publishCommmentValue: "",
      // 控制最底下评论编辑栏的弹窗
      publishCommentShowKey: false,
      allList: null,
      // 显示所有回复的弹窗
      allCommentShow: false,
    };
  },
  watch: {
    commentResult(newVal, oldVal) {
      oldVal;
      this.commentResult = newVal;
      console.log(this.commentResult);
    },
  },
  props: ["commentResult"],

  methods: {
    //点击进入个人信息中心页面,并携带个人ID过去,
    enterUserInfoView(itemId, itemLevel, itemAvatar, itemNickName) {
      this.$router.push({
        name: "userInfo",
        query: {
          userId: itemId,
          userLevel: itemLevel,
          userAvatar: itemAvatar,
          userNickName: itemNickName,
        },
      });
    },
    overlayClose() {
      // 回复评论的弹窗
      if (this.getCommentKey) {
        this.getCommentKey = !this.getCommentKey;
        history.back();
      }
      //  最底下评论编辑栏的弹窗
      if (this.publishCommentShowKey) {
        this.publishCommentShowKey = !this.publishCommentShowKey;
        history.back();
      }
      // 楼中楼回复编辑的弹窗
      if (this.getReplyCommentKey) {
        this.getReplyCommentKey = !this.getReplyCommentKey;
        history.back();
      }
      // 余下所有回复编辑弹窗
      if (this.getReplyAllCommentKey) {
        this.getReplyAllCommentKey = !this.getReplyAllCommentKey;
        this.allCommentShow = true;
        history.go(-1);
      }
    },
    // 拿到余下所有回复第几条评论的昵称,并打开弹窗
    giveReplyAllCommentNickName(nickName) {
      this.replyAllCommentValue = "";

      this.replyAllCommentNickName = nickName;

      this.getReplyAllCommentKey = !this.getReplyAllCommentKey;
      window.history.pushState(null, null, document.URL);
      // window.history.pushState(null, null, document.URL);
    },

    // 拿到回复楼中楼第几条评论的昵称,并打开弹窗
    giveReplyCommentNickName(nickName) {
      this.replyCommentValue = "";

      this.replyCommentNickName = nickName;

      this.getReplyCommentKey = !this.getReplyCommentKey;
      window.history.pushState(null, null, document.URL);
    },

    // 拿到判断是回复第几条评论的关键值,并打开弹窗
    giveIndex(index) {
      this.commentValue = "";
      console.log("index:", index);
      this.getCommentValue = index;
      this.commentNickName =
        this.commentResult.data.all.list[this.getCommentValue].nickName;
      this.getCommentKey = !this.getCommentKey;
      window.history.pushState(null, null, document.URL);
    },
    // 点击显示隐藏最底下的评论编辑栏弹窗
    publishCommentShow() {
      this.publishCommentShowKey = !this.publishCommentShowKey;
      window.history.pushState(null, null, document.URL);
    },
    // 滚动加载
    loadMore(e) {
      if (
        e.srcElement.scrollTop + e.srcElement.offsetHeight >=
        e.srcElement.scrollHeight
      ) {
        // 如果传来的数据有next这一项
        if (this.commentResult.data.all.next) {
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            loadingType: "spinner",
            position: "bottom",
            duration: 0,
          });
          this.axios
            .get(
              "https://apis.netstart.cn/yunyuedu" +
                this.commentResult.data.all.next
            )
            .then((res) => {
              this.commentResult.data.all.list =
                this.commentResult.data.all.list.concat(res.data.all.list);
              this.commentResult.data.all.next = res.data.all.next;

              this.$toast.clear();
            });
        } else {
          this.$toast({
            message: "我是有底线的!(｡･ω･｡)",
            position: "bottom",
          });
        }
      }
    },
    closeAllPopup() {
      if (this.allCommentShow) {
        this.allCommentShow = !this.allCommentShow;
        history.back();
      }
    },
    allShowPopup() {
      this.allCommentShow = true;
      window.history.pushState(null, null, document.URL);
    },
    //   点击请求余下回复的列表
    getAllcomment(url, index) {
      this.allShowPopup();
      this.$toast.loading({
        message: "剩余评论加载中...",
        forbidClick: true,
        loadingType: "spinner",
        position: "bottom",
        duration: 0,
      });
      this.axios.get(url).then((res) => {
        index;
        this.allList = null;
        this.allList = res.data.all.list;
        this.allList = this.allList.slice(2, this.allList.length);
        this.$toast.clear();
      });
    },
    // 时间戳转换为yyyy年mm月dd日 hh时mm分
    getDateTime(value) {
      // 时间戳转换为当前地区的标准时间
      let time = new Date(value);
      // 自带的etFullYear方法可以将标准时间返回一个表示年份的 4 位数字
      let year = time.getFullYear();
      //getMonth()方法可将标准时间返回表示月份的数字。返回值是0(一月)到11(十二月)之间的一个整数。
      let month = time.getMonth() + 1;
      //getDate()可返回标准时间的月份的某一天
      let date = time.getDate();
      // .getHours()可返回标准时间的小时字段。
      let hour = time.getHours();
      //
      let minute = time.getMinutes();
      // let second = time.getSeconds();
      // 获取当前标准时间 判断获得的时间是否小于当前年份
      let currentTime = new Date();
      let currentYear = currentTime.getFullYear();
      month = month < 10 ? "0" + month : month;
      date = date < 10 ? "0" + date : date;
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      if (year < currentYear) {
        let str =
          String(year) +
          "年" +
          String(month) +
          "月" +
          String(date) +
          "日" +
          " " +
          String(hour) +
          ":" +
          String(minute);

        return str;
      } else {
        let str =
          String(month) +
          "月" +
          String(date) +
          "日" +
          " " +
          String(hour) +
          ":" +
          String(minute);
        return str;
      }
    },
  },
  computed: {
    ...mapState(["activatedIdVuex"]),
  },
  activated() {
    this.skeletonShow = true;
    setTimeout(() => {
      this.skeletonShow = false;
    }, 1000);
  },
  deactivated() {
    console.log("这个是comment.vue开始失活啦!");
  },

  mounted() {
    setTimeout(() => {
      this.skeletonShow = false;
    }, 1000);

    window.addEventListener(
      "popstate",
      (e) => {
        if (
          this.allCommentShow == true &&
          this.getReplyAllCommentKey == false
        ) {
          this.allCommentShow = false;
        } else if (
          this.getReplyAllCommentKey == true &&
          this.allCommentShow == true
        ) {
          console.log(123123123);
          this.allCommentShow = true;
          e;
        }
        /* else if (this.allCommentShow == false) {
          this.allCommentShow = true;
        } */
        //  最底下评论编辑栏的弹窗
        if (this.publishCommentShowKey) {
          this.publishCommentShowKey = !this.publishCommentShowKey;
        }
        // 回复评论的弹窗
        if (this.getCommentKey) {
          this.getCommentKey = !this.getCommentKey;
        }

        // 楼中楼回复编辑的弹窗
        if (this.getReplyCommentKey) {
          this.getReplyCommentKey = !this.getReplyCommentKey;
        }
        // 余下所有回复编辑弹窗
        if (this.getReplyAllCommentKey == true) {
          // window.history.pushState(null, null, document.URL);
          this.getReplyAllCommentKey = !this.getReplyAllCommentKey;
          this.allCommentShow = true;
        }
      },
      false
    );
  },
  destroyed() {
    window.removeEventListener(
      "popstate",
      (e) => {
        if (
          this.allCommentShow == true &&
          this.getReplyAllCommentKey == false
        ) {
          this.allCommentShow = false;
        } else if (
          this.getReplyAllCommentKey == true &&
          this.allCommentShow == true
        ) {
          console.log(123123123);
          this.allCommentShow = true;
          e;
        }
        /* else if (this.allCommentShow == false) {
          this.allCommentShow = true;
        } */
        //  最底下评论编辑栏的弹窗
        if (this.publishCommentShowKey) {
          this.publishCommentShowKey = !this.publishCommentShowKey;
        }
        // 回复评论的弹窗
        if (this.getCommentKey) {
          this.getCommentKey = !this.getCommentKey;
        }

        // 楼中楼回复编辑的弹窗
        if (this.getReplyCommentKey) {
          this.getReplyCommentKey = !this.getReplyCommentKey;
        }
        // 余下所有回复编辑弹窗
        if (this.getReplyAllCommentKey == true) {
          // window.history.pushState(null, null, document.URL);
          this.getReplyAllCommentKey = !this.getReplyAllCommentKey;
          this.allCommentShow = true;
        }
      },
      false
    );
  },
};
</script>

