<template>
  <div v-if="column_three_book_sort" class="column_three">
    <!-- 推荐标题 -->
    <h1 class="title">{{ column_three_book_sort.name }}</h1>
    <ul>
      <!-- 遍历 -->
      <li
        v-for="(item, index) in column_three_book_sort.list"
        :key="index"
        @click="enterBookContent(item.id)"
      >
        <!-- 书籍封面 -->
        <img :src="item.cover + thumbnail" alt="书籍封面" @error="errorImg" />
        <!-- 书名 -->
        <h3>{{ item.title }}</h3>
        <!-- 作者/分类 -->
        <p>{{ item.author }}/{{ item.category }}</p>
        <!-- 小说简介 -->
        <p>
          {{ item.content }}
        </p>
      </li>
    </ul>
    <div class="underline"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  props: ["column_three_book_sort"],
  computed: {
    ...mapState(["thumbnail"]),
    // ...mapState(["column_three_book_sort"]),
  },
  watch: {
    column_three_book_sort(newVal, oldVal) {
      oldVal;
      this.column_three_book_sort = newVal;
    },
  },
  methods: {
    // 进入书籍内容
    enterBookContent(bookId) {
      this.$router.push({
        path: "/bookcontent",
        query: {
          id: bookId,
        },
      });
    },
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
  },
};
</script>

<style lang="less" scoped>
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
  margin-top: 10px;
}
.title {
  margin-bottom: 10px;
  margin-top: 10px;
  text-align: left;
  font-weight: bold;
  font-size: 20px;
  color: #333;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  word-wrap: break-word;
}
.column_three {
  margin-left: 2vw;
  margin-right: 2vw;
}

li {
  position: relative;
  // z-index: -1;
  margin-bottom: 25px;
  display: list-item;
  text-align: -webkit-match-parent;
}
img {
  text-align: left;
  float: left;
  width: 82px;
  height: 115px;
  margin-right: 15px;
  box-shadow: 0 5px 7px 0 rgb(0 0 0 / 9%), 0px 2px 2px 0 rgb(0 0 0 / 3%);
  border-radius: 4px;
}
h3 {
  text-align: left;
  font-weight: bold;
  font-size: 17px;
  color: #222;
  height: 20px;
  line-height: 20px;
  overflow: hidden;
  margin-bottom: 7px;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
p:nth-of-type(1) {
  text-align: left;
  line-height: 14px;
  height: 14px;
  margin-bottom: 9px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 12px;
  color: #999;
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
p:nth-of-type(2) {
  text-align: left;
  line-height: 18px;
  height: 36px;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 12px;
  color: #999;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
</style>