<template>
  <van-pull-refresh
    success-text="刷新成功!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!--轮播图组件 -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <van-swipe-item
          @click="enterBannerBookContent(item)"
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!-- 人气排行导航 -->
      <div class="ranking_nav">
        <ul>
          <li
            v-for="(item, index) in shortCut"
            :key="index"
            @click="enterChannelRank(item.url, item.title)"
          >
            <div class="yuan_color">
              <div class="rangking_icon">
                <img :src="item.cover" alt="" />
              </div>
            </div>
            <span>{{ item.name }}</span>
          </li>
        </ul>
      </div>
      <!-- 非常浅的下划线 -->
      <div class="underline"></div>
      <!-- 5书排序组件 -->
      <FiveBook :five_book_sort="five_book_sort" />
      <!-- 3书竖列排序组件1 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />
      <!-- 3书横向排序组件1 -->
      <RowThree :row_three_book_sort="row_three_book_sort" />
      <!--  -->

      <!-- 3书竖列排序组件2 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort2" />
      <!-- 3书竖列排序组件3 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort3" />
      <!-- 3书横向排序组件2 -->
      <RowThree :row_three_book_sort="row_three_book_sort2" />
      <!-- 3书竖列排序组件4 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort4" />
      <!-- 3书横向排序组件3 -->
      <RowThree :row_three_book_sort3="row_three_book_sort3" />
    </div>
    <BaseLine v-if="banner_cover.length > 0" />
    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";
import FiveBook from "./FiveBook.vue";
import ColumnThree from "./ColumnThree.vue";
import RowThree from "./RowThree.vue";
import BaseLine from "./BaseLine.vue";
export default {
  components: {
    FiveBook,
    ColumnThree,
    RowThree,
    BaseLine,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      // 五书数据
      five_book_sort: null,
      // 竖排三书数据
      column_three_book_sort: null,
      // 横排三书数据
      row_three_book_sort: null,
      /* */
      // 3书竖列排序组件2数据
      column_three_book_sort2: null,
      // 3书竖列排序组件3数据
      column_three_book_sort3: null,
      // 3书横向排序组件2数据
      row_three_book_sort2: null,
      // 3书竖列排序组件4数据
      column_three_book_sort4: null,
      // 3书横向排序组件3数据
      row_three_book_sort3: null,
    };
  },

  methods: {
    // 点击轮播图进入书籍
    enterBannerBookContent(book) {
      // 如果是正常书籍
      if (book.id) {
        this.$router.push({
          path: "/bookcontent",
          query: { id: book.id, homeKey: "/bookmall/selected" },
        });
      } else {
        // 如果是剧本杀或者其他什么鬼东西
        this.$router.push({
          name: "jubensha",
          query: { url: book.url, homeKey: "/bookmall/selected" },
        });
      }
    },
    // 点击首页排行进入排行榜
    enterChannelRank(url, title) {
      // 如果是女生-种田
      if (url.indexOf("catId=17") !== -1 || url.indexOf("catId=24") !== -1) {
        this.$router.push({
          name: "screenbook",
          query: {
            url: url,
            title: title,
          },
        });
      } else if (url.indexOf("son?uuid=83821f1710") != -1 && title == "完本") {
        this.$router.push({
          name: "publishrank",
          query: {
            url: url,
            title: "完本畅读",
          },
        });
      } else if (
        (url.indexOf("newsubject") != -1 && title == "奇文") ||
        (url.indexOf("newsubject") != -1 && title == "精品")
      ) {
        // 如果是奇文或者精品
        this.$router.push({
          name: "jubensha",
          query: { url: url, title: title, homeKey: "/bookmall/selected" },
        });
      } else {
        this.$router.push({
          name: "channelrank",
          query: {
            url: url,
          },
        });
      }
    },
    // 下拉刷新重新发送请求
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;
            console.log("selected-nav-information===>", this.information);
            // 获取精选页面数据内容
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[0].url}`
              )
              .then((result) => {
                // 精选-轮播图
                this.banner_cover = result.data.data.list[0].banners;
                console.log("this.banner_cover===>", this.banner_cover);
                // 精选-人气排行
                this.shortCut = result.data.data.list[1].shortCut;
                console.log("this.shortCut===>", this.shortCut);

                // 精选-获取五书排序的数据
                this.five_book_sort = result.data.data.list[2];

                // 精选-获取竖排三书排序的数据
                this.column_three_book_sort = result.data.data.list[3];

                // 精选-获取横排三书排序的数据
                this.row_three_book_sort = result.data.data.list[4];

                this.column_three_book_sort2 = result.data.data.list[5];
                this.column_three_book_sort3 = result.data.data.list[6];
                this.row_three_book_sort2 = result.data.data.list[7];
                this.column_three_book_sort4 = result.data.data.list[8];
                this.row_three_book_sort3 = result.data.data.list[9];
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },
  created() {
    //请求男生的数据
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 获取精选页面数据内容
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[0].url}`)
          .then((result) => {
            // 精选-轮播图
            this.banner_cover = result.data.data.list[0].banners;
            console.log("this.banner_cover===>", this.banner_cover);
            // 精选-人气排行
            this.shortCut = result.data.data.list[1].shortCut;
            console.log("this.shortCut===>", this.shortCut);

            // 精选-获取五书排序的数据
            this.five_book_sort = result.data.data.list[2];

            // 精选-获取竖排三书排序的数据
            this.column_three_book_sort = result.data.data.list[3];

            // 精选-获取横排三书排序的数据
            this.row_three_book_sort = result.data.data.list[4];

            this.column_three_book_sort2 = result.data.data.list[5];
            this.column_three_book_sort3 = result.data.data.list[6];
            this.row_three_book_sort2 = result.data.data.list[7];
            this.column_three_book_sort4 = result.data.data.list[8];
            this.row_three_book_sort3 = result.data.data.list[9];
            this.$toast.clear();
          });
      });
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  beforeRouteEnter(to, from, next) {
    to, from;

    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 0);
    });
  },
  beforeRouteLeave(to, from, next) {
    // 必须通过路由规则,离开该组件时被调用
    console.log("About-beforeRouteLeave", to, from);
    console.log("from.path:", from.path);
    this.$store.dispatch("getBackHomeKey", from.path);

    next();
  },
  watch: {
    // 利用watch监听性别值的变化,一旦有所改变,就重新发送请求
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 自定义加载图标
      this.$toast.loading({
        message: "切换中...",
        forbidClick: true,
        loadingType: "circular",
        duration: 0,
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 获取精选页面数据内容
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[0].url}`)
            .then((result) => {
              // 精选-轮播图
              this.banner_cover = result.data.data.list[0].banners;
              console.log("this.banner_cover===>", this.banner_cover);
              // 精选-人气排行
              this.shortCut = result.data.data.list[1].shortCut;
              console.log("this.shortCut===>", this.shortCut);

              // 精选-获取五书排序的数据
              this.five_book_sort = result.data.data.list[2];

              // 精选-获取竖排三书排序的数据
              this.column_three_book_sort = result.data.data.list[3];

              // 精选-获取横排三书排序的数据
              this.row_three_book_sort = result.data.data.list[4];

              this.column_three_book_sort2 = result.data.data.list[5];
              this.column_three_book_sort3 = result.data.data.list[6];
              this.row_three_book_sort2 = result.data.data.list[7];
              this.column_three_book_sort4 = result.data.data.list[8];
              this.row_three_book_sort3 = result.data.data.list[9];
              this.$toast.clear();
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// 解决底部导航栏遮住内容的div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// 建立一个div,生成非常非常浅的下划线,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
