<template>
  <!-- 这是书籍详情页点进来的目录页面 -->

  <div @scroll="loadMore($event)" class="BookCatalogView">
    <TitleBar :title="title" />
    <!--书籍封面图片 -->
    <div v-if="bookInfo" class="top">
      <div class="left_img">
        <img
          :src="bookInfo.coverImage + thumbnail"
          alt="小说封面"
          @error="errorImg"
        />
      </div>
      <div class="right_txt">
        <div class="frame">
          <p>{{ bookInfo.title }}</p>
          <span>{{ bookInfo.author }}</span>
        </div>
      </div>
    </div>
    <!-- 章节数量 -->
    <div
      style="
        padding-left: 5%;
        text-align: left;
        margin-left: 10px;
        margin-bottom: 20px;
      "
    >
      共{{ bookCatalog.length }}章
    </div>
    <div class="catalog">
      <ul>
        <!-- 遍历章节目录 -->
        <li
          v-for="(chapter, index) in bookCatalog.slice(startNum, endNum)"
          :key="chapter.secId"
          ref="chapter"
          @click="sendChapterIndex_secId(chapter.secId, index)"
        >
          <h2>
            <p>
              {{ chapter.title }}
              <span v-if="chapter.needPay" class="iconfont icon-suotou"></span>
            </p>
          </h2>
          <div class="dashline"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import TitleBar from "../components/TitleBar.vue";
export default {
  components: { TitleBar },
  data() {
    return {
      title: "目录",
      // 书籍目录数组
      bookCatalog: [],
      // 书籍相关信息
      bookInfo: null,
      startNum: 0,
      endNum: 100,
      newList: [],
      // 本地历史记录
      localHistory: {},
    };
  },
  watch: {
    // 利用watch监听结束值的变化,一旦有所改变,就重新发送请求
    endNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);
      // 自定义加载图标
      this.$toast.loading({
        message: "加载目录中...",
        forbidClick: true,
        loadingType: "circular",
        duration: 200,
      });
    },
  },
  computed: {
    ...mapState(["source_uuid"]),
    ...mapState(["thumbnail"]),
  },
  /* created() {
    // 自定义加载图标
    this.$toast.loading({
      message: "加载目录中...",
      forbidClick: true,
      loadingType: "circular",
      duration: 0,
    });
    // 获取到书籍章节列表
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
      )
      .then((res) => {
        // 书籍目录赋予bookCatalog
        this.$nextTick(() => {
          if (res.data.data == null) {
            this.$toast({
              message:
                "该书籍ID只有XML接口可以看目录,但请求不到章节内容,那干脆目录页也别看了!",
              position: "bottom",
            });
            return;
          } else {
            this.bookCatalog = res.data.data.catalog;
            this.bookInfo = res.data.data.book;
            this.$toast.clear();
          }
        });
      });
  }, */
  // 激活
  activated() {},
  beforeRouteEnter(to, from, next) {
    console.log(to);
    console.log(from);
    if (from.path == "/bookcontent") {
      console.log("应当无事发生");
      next();
    } else {
      next((vm) => {
        if (vm.source_uuid != vm.$route.query.id) {
          console.log(
            "目录(或者说内容)页面被激活了,但是vm.source_uuid != vm.$route.query.id"
          );
          // 自定义加载图标
          vm.$toast.loading({
            message: "正在加载新的目录...",
            forbidClick: true,
            loadingType: "circular",
            duration: 0,
          });
          // 获取到书籍章节列表
          vm.axios
            .get(
              `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${vm.$route.query.id}`
            )
            .then((res) => {
              // 这个目录页我注释掉了cretaed,因为那样会请求2次目录,但如果有什么报错或者页面不对劲,还是改回来吧(除了书籍详情页--进入目录--点击章节--点击评论区---返回小说内容---返回目录页--会无法点击目录页的章节了.这个情况不是created造成的)
              // 书籍目录赋予bookCatalog
              vm.$nextTick(() => {
                if (res.data.data == null) {
                  vm.$toast({
                    message:
                      "该书籍ID只有XML接口可以看目录,但请求不到章节内容,那干脆目录页也别看了!",
                    position: "bottom",
                    duration: 2500,
                  });
                  return;
                } else {
                  vm.bookCatalog = res.data.data.catalog;
                  vm.bookInfo = res.data.data.book;
                  vm.$toast.clear();
                }
              });
            });
        }
      });
    }
  },
  // 失活时
  deactivated() {},
  mounted() {},
  methods: {
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 点击章节传递索引并高亮
    sendChapterIndex_secId(secId, index) {
      index;
      // 阅读历史记录
      // 本地历史记录不需要push进去,因为没有排序的打算
      this.localHistory[this.$route.query.id] = {
        bookId: this.$route.query.id,
        secId: secId,
        time: new Date().getTime(),
      };
      localStorage.setItem("localHistory", JSON.stringify(this.localHistory));
      // 进入路由
      this.$router.push({
        path: "/bookcontent",
        query: {
          id: this.$route.query.id,
        },
      });
    },

    // 加载更多
    loadMore(e) {
      if (
        e.srcElement.scrollTop + e.srcElement.offsetHeight >=
        e.srcElement.scrollHeight
      ) {
        if (this.endNum < this.bookCatalog.length) {
          this.endNum = this.endNum + 250;
        } else {
          this.$toast({
            message: "已经加载全部章节!",
            position: "bottom",
          });
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.BookCatalogView {
  height: 100vh;
  overflow: scroll;
  .top {
    display: flex;
    margin-left: 5%;
    margin-top: 7%;
    .left_img {
      img {
      }
    }
    .right_txt {
      // background: red;
      // padding-top: 40px;
      margin-left: 5%;
      width: 100%;
      text-align: left;
      .frame {
        margin-top: 20px;
        // background: red;
        p {
          font-size: 20px;
          margin-bottom: 10px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
        span {
          color: gray;
          font-size: 16px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .catalog {
    // height: 100%;
    // overflow: auto;
    ul {
      text-align: left;
      padding: 0 10px;
      li {
        padding-left: 5%;
        h2 {
          margin-bottom: 5px;
          p {
            position: relative;
            span {
              position: absolute;
              right: 20px;
            }
          }
        }

        .dashline {
          border: 1px gray dashed;
          margin-bottom: 5px;
        }
      }
    }
  }
  .highLight {
    color: red;
  }
}
</style>