<template>
  <div>
    <van-row style="padding: 0 5%">
      <van-col span="5">
        <p style="height: 45px; line-height: 45px; text-align: left">书架</p>
      </van-col>
      <van-col
        span="17"
        offset="1"
        style="height: 45px; line-height: 45px; text-align: right"
      >
        <span @click="EnterSearch">
          <i style="font-size: 22px" class="iconfont icon-sousuo"></i>
        </span>
        <!-- 右上角更多选项----批量管理----历史记录 -->
        <span style="margin-left: 10px">
          <van-popover
            v-model="showPopover"
            trigger="click"
            :actions="actions"
            @select="onSelect"
            placement="bottom-end"
            :overlay="true"
            :offset="[14, 8]"
          >
            <template #reference>
              <van-icon size="22" name="wap-nav" />
            </template>
          </van-popover>
        </span>
      </van-col>
    </van-row>
    <!-- 非常浅的下划线 -->
    <div ref="vancol" class="underline"></div>
    <!-- 书城为空的时候的去选书按钮 -->
    <van-row v-if="localBookShelfx == null || localBookShelfx == '[]'">
      <van-empty image="search" description="您的书架是空的" />
      <van-col style="margin-top: 1vh" span="24">
        <van-button
          @click="goHome()"
          width="100"
          color="rgb(211 43 37);"
          round
          type="info"
          >去选书</van-button
        >
      </van-col>
    </van-row>
    <!-- 书架的书籍 -->
    <van-row
      ref="vancol2"
      v-else
      gutter="20"
      style="
        padding: 0 5%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      "
    >
      <van-col
        @touchstart.native="gtouchstart(item.id, $event)"
        @touchmove.native="gtouchmove($event)"
        @touchend.native="showDeleteButton(item.id, $event)"
        span="8"
        v-for="(item, index) in bookShelfList"
        :key="index"
        style="margin-bottom: 10px; width: 30%"
        class="book_item"
      >
        <img
          class="book_img"
          width="100%"
          :src="item.cover + '?imageView&thumbnail=240y336'"
          @error="errorImg"
        />

        <p
          class="book_name"
          style="
            text-align: left;
            font-size: 14px;
            margin-top: 10px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden; ;
          "
        >
          {{ item.title }}
        </p>
      </van-col>

      <van-col span="8" style="margin-bottom: 10px; width: 30%"> </van-col>
    </van-row>
    <!-- 长按书籍出现的弹出窗口:继续阅读-----书籍详情------移出书架 -->
    <van-popup
      position="bottom"
      v-model="longPressShow"
      style="text-align: left"
      @close="longPressShow = false"
    >
      <van-cell
        @click="goBookContent(popupBookId)"
        style="padding-left: 30px"
        title="继续阅读"
        icon="orders-o"
      >
      </van-cell>
      <van-cell
        @click="goBookDeatil(popupBookId)"
        style="padding-left: 30px"
        title="书籍详情"
        icon="description"
      >
      </van-cell>
      <van-cell
        @click="removeBook(popupBookId)"
        style="padding-left: 30px"
        title="移出书架"
        icon="delete-o"
      >
      </van-cell>
    </van-popup>
    <div class="bottom_trim" style="margin-top: 50px"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Dialog } from "vant";
export default {
  components: { [Dialog.Component.name]: Dialog.Component },

  data() {
    return {
      // 控制长按窗口的显示隐藏
      longPressShow: false,
      // 弹窗出来后传过来的书籍ID
      popupBookId: null,
      bookShelfList: [],
      activatedShelfLIst: [],
      // 右上角下拉菜单
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [
        { text: "批量管理", icon: "bars" },
        { text: "历史记录", icon: "clock-o" },
      ],
    };
  },
  computed: {
    ...mapState(["back_home_key"]),
    ...mapState(["localBookShelfx"]),
    // ...mapState(["source_uuid"]),
  },
  created() {},
  // 进入路由之前
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      if (vm.activatedShelfLIst !== vm.localBookShelfx) {
        vm.$store.dispatch(
          "updateLocalBookShelf",
          localStorage.getItem("localShelf")
        );
        if (vm.localBookShelfx != null && vm.localBookShelfx != "[]") {
          vm.axios
            .get(
              `https://apis.netstart.cn/yunyuedu/shelf/detail.json?ids=${vm.localBookShelfx}`
            )
            .then((res) => {
              vm.bookShelfList = res.data.data.list.reverse();
            });
        }
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;

    this.activatedShelfLIst = this.localBookShelfx;
    next();
  },
  // ////////
  methods: {
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 将单独选中的书籍移出书架
    removeBook(bookId) {
      bookId;
      // 现将这个弹窗关闭
      this.longPressShow = false;
      Dialog.confirm({
        title: "移出书架",
        message: "是否将该选中的书籍移出书架?",
      })
        .then(() => {
          this.bookShelfList.forEach((item, index) => {
            if (item.id == bookId) {
              this.bookShelfList.splice(index, 1);
            }
          });

          var newLocalBookShelfx = JSON.parse(this.localBookShelfx);
          newLocalBookShelfx.forEach((item, index) => {
            if (item.id == bookId) {
              newLocalBookShelfx.splice(index, 1);
            }
          });

          this.$store.dispatch(
            "updateLocalBookShelf",
            JSON.stringify(newLocalBookShelfx)
          );
          localStorage.setItem("localShelf", this.localBookShelfx);
          this.$toast("删除成功");
        })

        .catch(() => {
          // on cancel
        });
    },
    // 进入书籍详情界面
    goBookDeatil(bookId) {
      this.longPressShow = false;
      setTimeout(() => {
        this.$router.push({
          name: "bookdetail",
          query: {
            id: bookId,
          },
        });
      }, 100);
    },
    // 单独点击书籍事件
    goBookContent(bookId) {
      this.longPressShow = false;
      setTimeout(() => {
        this.$router.push({
          path: "/bookcontent",
          query: { id: bookId },
        });
      }, 100);
    },
    //长按事件（起始）
    gtouchstart(bookId, e) {
      e;
      console.log("准备开始长按!");

      var self = this;
      this.timeOutEvent = setTimeout(function () {
        self.longPress(bookId);
      }, 500); //这里设置定时器，定义长按500毫秒触发长按事件
      return false;
    },
    //手释放，如果在500毫秒内就释放，则取消长按事件，此时可以执行onclick应该执行的事件
    showDeleteButton(bookId, e) {
      clearTimeout(this.timeOutEvent); //清除定时器
      if (this.timeOutEvent != 0) {
        //这里写要执行的内容（如onclick事件）
        console.log("点击但未长按");
        this.goBookContent(bookId);
      }
      if (e.cancelable) {
        e.preventDefault(); //阻止默认事件
      }
    },
    //如果手指有移动，则取消所有事件，此时说明用户只是要移动而不是长按
    gtouchmove(e) {
      e;
      clearTimeout(this.timeOutEvent); //清除定时器
      this.timeOutEvent = 0;
    },
    //真正长按后应该执行的内容
    longPress(bookId) {
      bookId;
      // 弹窗出来后把所点击的书籍的ID传给他,作临时记录
      this.popupBookId = bookId;
      this.timeOutEvent = 0;
      //执行长按要执行的内容，如弹出菜单
      this.longPressShow = true;
    },
    // 右上角下拉菜单方法
    onSelect(action, index) {
      if (index == 0) {
        console.log(action.text, index);
        setTimeout(() => {
          this.$router.push({
            name: "batchmanage",
          });
        }, 100);
      } else if (index == 1) {
        console.log(action.text, index);
        setTimeout(() => {
          this.$router.push({
            name: "readhistory",
          });
        }, 100);
      }
    },
    // 从书架进入搜索栏
    EnterSearch() {
      this.$router.push({
        name: "searchHint",
      });
    },
    // 书架空时去首页
    goHome() {
      this.$router.push(this.back_home_key).catch(() => {});
    },
  },
  mounted() {},
};
</script>

<style lang="less">
.van-button--info {
  width: 100px;
}
@popover-action-width {
  width: 218px;
}
// 建立一个div,生成非常非常浅的下划线,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
  margin-bottom: 6px;
}
</style>
