<template>
  <div>
    <TitleBar :title="title" />
    <div class="resp-container">
      <iframe
        class="resp-iframe"
        :src="$route.query.url"
        frameborder="0"
        scrolling="no"
        width="100%"
        height="100%"
      ></iframe>
    </div>
  </div>
</template>

<script>
import TitleBar from "../components/TitleBar.vue";
export default {
  data() {
    return {
      title: "广告与游戏",
    };
  },
  components: { TitleBar },
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      if (vm.$route.query.title) {
        vm.title = vm.$route.query.title;
      } else {
        vm.title = "广告与游戏";
      }
    });
  },
};
</script>

<style>
.resp-container {
  width: 100vw;
  height: 100vh;
}
</style>