<template>
  <!-- 引用vant的Popup,van-cell组件,van-row,van-col -->
  <div>
    <!-- 从路由接受到的书籍id -->

    <div
      class="box"
      ref="box"
      :class="{ dark: dark_light === 'dark' }"
      @touchstart.passive="touchPush($event)"
      @touchend.passive="touchLift($event)"
      @mousedown="shubiaoDown($event)"
      @mouseup="shubiaoUp($event)"
    >
      <div class="wapper" ref="wapper">
        <div
          class="content"
          v-show="isShow"
          ref="content"
          :class="{
            dark: dark_light === 'dark',
            normal: fontsize === 'normal',
            large: fontsize === 'large',
            small: fontsize === 'small',
          }"
        ></div>
      </div>

      <div id="lc" @click="pre()"></div>
      <div id="rc" @click="next()"></div>
      <!-- 主导航弹出栏组件 -->
      <div id="main_nav_show">
        <!-- 控制主导航是否弹出的控制器 -->
        <van-cell class="main_show_control" @click="mainShowPopup"> </van-cell>
        <!-- 顶部导航栏容器 -->
        <van-popup
          class="top_show_container"
          :class="{ dark: dark_light === 'dark' }"
          v-model="mainShow"
          :overlay-style="{ background: 'rgba(0,0,0,0)' }"
          position="top"
          :style="{
            width: '100%',
            height: '2.75rem',
            padding: ' 0.6875rem 0 0.625rem 0',
          }"
        >
          <!-- 顶部导航栏的内容:每个元素的两侧间隔相等 -->
          <van-row class="top_show_content" type="flex" justify="space-around">
            <!-- 顶部内容的第1列:返回上一级页面按钮  -->
            <van-col @click="backOne" span="16">
              <div
                style="text-align: left; padding-left: 6%; font-size: 20px"
                class="top_show_column_item iconfont icon-xiangzuojiantou"
              ></div>
            </van-col>
            <!-- 顶部内容的第2列:评论区 -->
            <van-col span="4">
              <div
                @click="enterBookcomment()"
                style="font-size: 20px"
                class="top_show_column_item iconfont icon-pinglun"
              ></div>
            </van-col>
            <!-- 顶部内容的第3列:更多 -->
            <van-col span="4">
              <div
                @click="showMore()"
                style="font-size: 20px; padding-right: 19px"
                class="top_show_column_item iconfont icon-gengduo-copy"
              ></div>
            </van-col>
          </van-row>
        </van-popup>

        <!-- 点击[更多]的弹窗容器 -->
        <van-popup
          :class="{ dark: dark_light === 'dark' }"
          class="more_show_container"
          v-model="moreShow"
          position="bottom"
          :style="{ height: 'auto' }"
        >
          <div class="showMore">
            <van-row class="bookDetail" @click="enterBookDetai">
              <p>书籍详情</p></van-row
            >
            <van-row class="shareBook" @click="shareShow">
              <p>分享书籍</p></van-row
            >
            <van-row class="bookShelf" @click="collect()" v-if="needCollect">
              <p>加入书架</p></van-row
            >
            <van-row class="bookShelf" v-else>
              <p style="color: gray">√已在书架</p></van-row
            >
          </div>
        </van-popup>
        <!-- 主部位导航栏容器 -->
        <van-popup
          :overlay-style="{ background: 'rgba(0,0,0,0)', margin: '8vh 0 0 0' }"
          :class="{ dark: dark_light === 'dark' }"
          class="main_show_container"
          v-model="mainShow"
          position="bottom"
          :style="{
            width: '100%',
            padding: ' 1rem 0px 0.825rem 0',
          }"
        >
          <van-row>
            <van-col @click="prevChapter()" span="4" offset="2">
              <p>上一章</p>
            </van-col>
            <van-col
              @click="progress()"
              style="margin-top: 5px"
              span="10"
              offset="1"
            >
              <van-progress :percentage="50" />
            </van-col>
            <van-col @click="nextChapter()" span="4" offset="1">
              <p>下一章</p>
            </van-col>
          </van-row>
          <!-- 导航栏的内容:每个元素的两侧间隔相等 -->
          <van-row class="main_show_content" type="flex" justify="space-around">
            <!-- 内容的第1列 -->
            <van-col span="6">
              <div @click="leftShowPopup" class="main_show_column_item">
                <span class="iconfont icon-mulu"></span>
                <p>目录</p>
              </div>
            </van-col>
            <!-- 内容的第2列 -->
            <van-col span="6">
              <div @click="settingShowPopup" class="main_show_column_item">
                <span class="iconfont icon-shezhi"></span>
                <p>设置</p>
              </div></van-col
            >
            <!-- 内容的第3列 -->
            <van-col span="6">
              <div
                v-if="dark_light === 'light'"
                class="main_show_column_item"
                @click="toDark"
              >
                <span class="iconfont icon-yueduye-yejianmoshi"></span>
                <p>夜间模式</p>
              </div>
              <div v-else class="main_show_column_item" @click="toLight">
                <span class="iconfont icon-rijianmoshi"></span>
                <p>日间模式</p>
              </div>
            </van-col>
          </van-row>
        </van-popup>
        <!-- 左侧目录页容器 -->
        <van-popup
          :class="{ dark: dark_light === 'dark' }"
          class="left_show_container"
          v-model="leftShow"
          @click-overlay="
            $router
              .replace({
                path: '/bookcontent',
                query: { id: $route.query.id },
              })
              .catch(() => {})
          "
          position="left"
          :style="{ height: '100%' }"
        >
          <!--目录内容 记得宽需要设置短一些,下面要用组件百分百宽度的,到时候复用 -->
          <div class="left_catalog">
            <BookCatalogView
              ref="bookcatalogview"
              @clickChapter="loadCatalogChapter"
              :highLighIndex="highLighIndex"
            />
          </div>
        </van-popup>
        <!-- 主部位点击[设置字体]的弹窗容器 -->
        <van-popup
          v-model="settingShow"
          position="bottom"
          class="setting_show_container"
          :class="{ dark: dark_light === 'dark' }"
        >
          <div style="display: flex; margin-left: 20px; padding: 20px 0 25px 0">
            <div style="margin-right: 13px"><p>字体大小:</p></div>
            <div>
              <!-- 点击增加字体型号 -->
              <div>
                <p v-if="fontsize === 'small'">
                  <button
                    style="
                      color: gray;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A-
                  </button>
                  <span> 入微 </span>
                  <button
                    @click="fontSizeAdd"
                    style="
                      color: black;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A+
                  </button>
                </p>
                <p v-if="fontsize === 'normal'">
                  <button
                    @click="fontSizeSub"
                    style="
                      color: black;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A-
                  </button>
                  <span> 普通 </span>
                  <button
                    @click="fontSizeAdd"
                    style="
                      color: black;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A+
                  </button>
                </p>
                <p v-if="fontsize === 'large'">
                  <button
                    @click="fontSizeSub"
                    style="
                      color: black;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A-
                  </button>
                  <span> 显眼 </span>
                  <button
                    style="
                      color: gray;
                      background: white;
                      border-radius: 13px;
                      padding: 2px 15px;
                      border: 1px solid;
                    "
                  >
                    A+
                  </button>
                </p>
              </div>
            </div>
          </div>
        </van-popup>
        <input
          id="input_url"
          v-model="product_url"
          style="opacity: 0; position: absolute"
          type="text"
        />
        <!-- 分享面板弹窗 -->
        <van-share-sheet
          v-model="showShare"
          title="立即分享给好友"
          :options="options"
          @select="onSelect"
        />
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import BookCatalogView from "./BookCatalogView.vue";
export default {
  components: {
    BookCatalogView,
  },
  data() {
    return {
      num: 0,
      endX: null,
      endY: null,
      startX: null,
      atartY: null,
      downA: null,
      isShow: false,

      // 控制主导航是否显示
      mainShow: false,
      // 控制左侧目录页是否显示
      leftShow: false,
      // 控制更多页面是否显示
      moreShow: false,
      // 控制设置页面是否显示
      settingShow: false,
      // 本地书架
      localShelf: [],
      // 书架的书籍ID的合集
      shelfArray: [],
      // 是否需要收藏进书架
      needCollect: true,
      // 本地历史记录
      localHistory: {},
      nullObject: {},
      // 目录数据
      bookList: [],
      // 章节ID
      chapterId: null,
      // 章节内容
      chapterContent: null,
      //章节secId目录合集
      chapterSecIdArray: [],
      // 高亮章节的索引
      highLighIndex: null,
      // 控制是日间模式还是夜间模式
      dark_light: "",
      // fontsize
      fontsize: "",

      // 分享
      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
      //当前位置URL
      product_url: "",
    };
  },
  computed: {
    ...mapState(["source_uuid"]),
  },
  activated() {
    if (this.$route.query.id !== this.source_uuid) {
      // 如果不一样,则回到最开始
      // 解决A书看了几页，去看别的书籍也会卡在第几页的进度这个问题
      var content = document.getElementsByClassName("content")[0];
      this.num = 0;
      content.style.transform = "translateX(" + this.num + "px)";
    }
  },
  // 切换到此路由之前
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      console.log(to, from);
      if (from.path !== "/bookcomment") {
        vm.$toast("内容加载中...");
      }
      // 进来前看字体的大小,没有设置的话就默认为normal
      vm.fontsize = localStorage.getItem("fontsize") || "normal";
      // 进来前看本地光暗那里看是夜间还是日间,如果没有设置则设置为默认值light
      vm.dark_light = localStorage.getItem("dark_light") || "light";

      // 每本书进来之前,先看看有没有收藏在书架(遍历本地书架)
      vm.localShelf = JSON.parse(localStorage.getItem("localShelf") || "[]");
      // 遍历它
      if (vm.localShelf.length > 0) {
        console.log("vm.localShelf.length>0");
        vm.localShelf.forEach((item) => {
          vm.shelfArray.push(item.id);
        });
        vm.shelfArray.indexOf(to.query.id) !== -1
          ? (vm.needCollect = false)
          : (vm.needCollect = true);
      } else {
        vm.needCollect = true;
      }
      // 去本地历史记录对比下有没有
      // 先获取本地历史记录
      if (to.query.id !== vm.$store.state.source_uuid) {
        // 如果不一样,则重新请求数据
        console.log("ID不一样了,重新请求数据");

        vm.localHistory = JSON.parse(
          localStorage.getItem("localHistory") || "{}"
        );
        var idArray = [];
        for (const itemKey in vm.localHistory) {
          idArray.push(itemKey);
        }
        // 判断本地历史记录里面是否有这本书
        if (idArray.indexOf(to.query.id) == -1) {
          console.log("历史没有这本书,那么加载第一章");
          // 既然书架里没有和历史记录里都没有,必定是第一次观看,那就判断进来的书籍ID是否和刚才的一样
          // 能来到这里,证明就是第一次看这本书,就只加载第一章给它
          vm.loadChapter();
        } else {
          console.log("历史有这本书,就加载本地历史记录");
          // 如果只是回去然后再重新进来,那就不用再重新请求;如果是去看了别的书籍,再回来这本书的话,就重新请求

          if (to.query.id == vm.$store.state.source_uuid) {
            //回去然后再重新进来,那就不用再重新请求
            console.log("没有去过别的书籍,只是单纯回退再进来");
          } else {
            console.log("去过别的书籍了,重新请求数据");
            // 加载历史记录的书籍ID和章节ID

            vm.loadHistoryChapter(
              to.query.id,
              vm.localHistory[to.query.id].secId
            );
          }
        }
      } else {
        console.log("ID一样的,并不需要重新请求数据");
      }
    });
  },
  // 离开路由之前
  beforeRouteLeave(to, from, next) {
    to, from;
    console.log(to);
    console.log(from);
    if (to.path !== "/bookcatalog" && to.path !== "/bookdetail") {
      // 离开之前将路由参数更新给VUEX,并要求要去的地方不能是bookcatalog
      this.$store.dispatch("getSourceUuId", from.query.id);
    }

    next();
  },
  methods: {
    // 进度条
    progress() {
      this.$toast("暂不支持");
    },
    popstateFun() {
      if (this.showShare == true) {
        this.showShare = false;
      }
    },
    // 点击分享
    shareShow() {
      this.showShare = true;
      this.moreShow = false;
      //让弹窗跳出来的时候就往浏览器跳转记录中添加一条空记录
      window.history.pushState(null, null, document.URL);
    },
    // 分享
    onSelect(option) {
      this.showShare = false;
      if (option.name == "复制链接") {
        var input = $("#input_url");
        input.select();
        document.execCommand("Copy");
        this.$toast("链接复制成功");
      } else {
        this.$toast("暂时仅支持复制链接");
      }
    },
    // 字体变大
    fontSizeAdd() {
      if (this.fontsize == "normal") {
        this.fontsize = "large";
        localStorage.setItem("fontsize", "large");
      } else if (this.fontsize == "small") {
        this.fontsize = "normal";
        localStorage.setItem("fontsize", "normal");
      }
    },
    // 字体变小
    fontSizeSub() {
      if (this.fontsize == "normal") {
        this.fontsize = "small";
        localStorage.setItem("fontsize", "small");
      } else if (this.fontsize == "large") {
        this.fontsize = "normal";
        localStorage.setItem("fontsize", "normal");
      }
    },
    // 点击设置,出来弹窗
    settingShowPopup() {
      this.settingShow = true;
      this.mainShow = false;
    },
    // 变为夜间模式
    toDark() {
      localStorage.setItem("dark_light", "dark");
      this.dark_light = "dark";
    },
    // 变为日间模式
    toLight() {
      localStorage.setItem("dark_light", "light");
      this.dark_light = "light";
    },
    // 进入书籍详情界面,但要先关闭弹窗
    enterBookDetai() {
      this.moreShow = false;
      setTimeout(() => {
        this.$router.push({
          path: "/bookdetail",
          query: {
            id: this.$route.query.id,
          },
        });
      }, 100);
    },
    // backOne;返回上一页,并先关闭主弹窗
    backOne() {
      this.mainShow = false;
      this.$router.go(-1);
    },
    // showMore:点击更多展示弹窗,关闭其他弹窗
    showMore() {
      this.moreShow = true;
      this.mainShow = false;
    },
    // 刚进来的时候,加载了小说内容,需要目录对应的章节高亮!这个就是获取高亮索引方法
    getHighLightIndex(secId) {
      // 先请求目录
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
        )
        .then((res) => {
          // 目录数据
          this.bookList = res.data.data.catalog;
          // 清空下这个章节ID合集数组
          this.chapterSecIdArray = [];
          this.bookList.forEach((chapter) => {
            // 将所有章节ID存进数组
            this.chapterSecIdArray.push(chapter.secId);
          });
          // 获取需要高亮章节的索引
          this.highLighIndex = this.chapterSecIdArray.indexOf(secId);
          console.log("highLighIndex======>", this.highLighIndex);
        });
    },

    //点击目录章节加载小说内容
    loadCatalogChapter(index, secId, needPay) {
      this.chapterId = secId;
      if (needPay == false || needPay == undefined) {
        this.chapterContent = "文章加载中,请稍候......";
        this.$refs.content.innerHTML = this.chapterContent;
        this.loadHistoryChapter(this.$route.query.id, secId);
        var content = document.getElementsByClassName("content")[0];
        if (this.num == 0) {
          console.log("相安无事");
        } else if (this.num < 0) {
          console.log("点击目录里的新章节之前就享向右滑动过了!");
          this.num = 0;
          content.style.transform = "translateX(" + this.num + "px)";
        }
      } else {
        // 当你发现需要付费的时候
        this.chapterContent = "文章加载中,请稍候.....!";
        this.$refs.content.innerHTML = this.chapterContent;

        setTimeout(() => {
          this.chapterContent = "对不起,该章节为收费章节,请购买后再阅读!";
          this.$refs.content.innerHTML = this.chapterContent;
        }, 1500);
        this.readHistory();
      }
    },
    // 控制vant主导航栏是否弹出
    mainShowPopup() {
      this.mainShow = true;
    },
    // 控制左侧目录导航栏是否弹出
    leftShowPopup() {
      this.leftShow = true;
      this.mainShow = !this.mainShow;
      this.$router.replace({
        path: "/bookcontent/bookcatalog",
        query: { id: this.$route.query.id },
      });
    },

    shubiaoDown(e) {
      this.$nextTick(() => {
        var evt = window.event || e || arguments[0];
        this.downA = evt.screenX;
        // console.log("mouseDown事件", e);
      });
    },
    shubiaoUp(e) {
      this.$nextTick(() => {
        var evt = window.event || e || arguments[0];
        var b = evt.screenX;
        if (this.downA > b) {
          this.moveLeft();
        }
        if (this.downA < b) {
          this.moveRight();
        }
      });
    },
    touchPush(e) {
      this.$nextTick(() => {
        var evt = window.event || e || arguments[0];
        //console.log('手指按下的时候');
        //console.log(event);
        this.startX = evt.touches[0].pageX;
        this.startY = evt.touches[0].pageY;
      });
    },
    touchLift(e) {
      this.$nextTick(() => {
        var evt = window.event || e;
        //console.log('手指抬起的时候');
        //console.log(event);
        this.endX = evt.changedTouches[0].pageX;
        this.endY = evt.changedTouches[0].pageY;
        var x = this.endX - this.startX;
        var y = this.endY - this.startY;
        var absX = Math.abs(x) > Math.abs(y);
        /*  absY没有用到
      var absY = Math.abs(y) > Math.abs(x);
      console.log(absY); */
        if (x > 0 && absX) {
          //console.log('右滑');
          this.moveRight();
        } else if (x < 0 && absX) {
          //console.log('左滑');
          this.moveLeft();
        }
      });
    },
    // 点击左侧
    pre() {
      this.$nextTick(() => {
        var content = document.getElementsByClassName("content")[0];
        if (this.num < 0) {
          this.num = this.num + this.$refs.box.offsetWidth;
          content.style.transform = "translateX(" + this.num + "px)";
        } else if (this.num == 0) {
          if (this.highLighIndex > 0) {
            this.prevChapter();
          } else {
            this.$toast({
              message: "已经是第一章了!",
              position: "bottom",
            });
          }
        }
        return this.num;
      });
    },
    // 点击右侧
    next() {
      this.$nextTick(() => {
        var content = document.getElementsByClassName("content")[0];
        if (
          this.num >
          -this.$refs.content.scrollWidth + this.$refs.box.offsetWidth
        ) {
          //当我们还有得滚动的时候
          this.num = this.num - this.$refs.box.offsetWidth;
          content.style.transform = "translateX(" + this.num + "px)";
        } else if (this.num < 320) {
          // 当我们发现卷轴的可滚动距离已经差不多了

          if (this.highLighIndex < this.bookList.length - 1) {
            this.nextChapter();
          } else if (
            this.highLighIndex > this.bookList.length - 1 ||
            this.highLighIndex == this.bookList.length - 1
          ) {
            this.$toast({
              message: "已经是最后一章了",
            });
          }
        }
        return this.num;
      });
    },
    // 滑到上一页
    moveRight() {
      this.$nextTick(() => {
        var content = document.getElementsByClassName("content")[0];
        if (this.num < 0) {
          this.num = this.num + this.$refs.box.offsetWidth;
          content.style.transform = "translateX(" + this.num + "px)";
        } else if (this.num == 0) {
          if (this.highLighIndex > 0) {
            this.prevChapter();
          } else {
            this.$toast({
              message: "已经是第一章了!",
              position: "bottom",
            });
          }
        }
        return this.num;
      });
    },
    // 滑到下一页
    moveLeft() {
      this.$nextTick(() => {
        var content = document.getElementsByClassName("content")[0];
        if (
          this.num >
          -this.$refs.content.scrollWidth + this.$refs.box.offsetWidth
        ) {
          this.num = this.num - this.$refs.box.offsetWidth;
          content.style.transform = "translateX(" + this.num + "px)";
        } else if (this.num < 320) {
          if (this.highLighIndex < this.bookList.length - 1) {
            this.nextChapter();
          } else if (
            this.highLighIndex > this.bookList.length - 1 ||
            this.highLighIndex == this.bookList.length - 1
          ) {
            this.$toast({
              message: "已经是最后一章了",
            });
          }
        }
        return this.num;
      });
    },
    // 进去评论区
    enterBookcomment() {
      this.mainShow = false;
      setTimeout(() => {
        this.$router.push({
          name: "bookcomment",
          query: { id: this.$route.query.id },
        });
      }, 100);
    },
    // 根据历史记录里的书籍ID和章节ID请求内容
    loadHistoryChapter(bookId, chapterId) {
      this.chapterId = chapterId;
      this.getHighLightIndex(chapterId);
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/reader/book/content.json?source_uuid=${bookId}&content_uuid=${chapterId}`
        )
        .then((res) => {
          this.$nextTick(() => {
            this.isShow = false;
            this.chapterContent = "";
            this.$refs.content.innerHTML = this.chapterContent;
            this.isShow = true;
            if (res.data.data) {
              if (res.data.data.content) {
                this.chapterContent = res.data.data.content;
                this.$refs.content.innerHTML += this.chapterContent;
                this.isShow = true;
                this.readHistory();
                console.log(this.$refs.content);
              } else {
                this.chapterContent = "对不起,该章节为收费章节,请购买后再阅读!";
                this.$refs.content.innerHTML = this.chapterContent;
                this.isShow = true;
                this.readHistory();
              }
            } else {
              this.chapterContent = "对不起,该章节为收费章节,请购买后再阅读!";
              this.$refs.content.innerHTML = this.chapterContent;
              this.isShow = true;
              this.readHistory();
            }
          });
        });
    },

    // 加载第一章节
    loadChapter() {
      this.chapterContent = "正在加载第一章,请稍候......";
      this.$refs.content.innerHTML = this.chapterContent;
      // 先请求目录
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=${this.$route.query.id}`
        )
        .then((res) => {
          this.$nextTick(() => {
            // 目录数据
            this.bookList = res.data.data.catalog;
            // 要目录里的第一章节ID
            this.chapterId = this.bookList[0].secId;
            // 既然第一次,那索引必定默认为0
            this.highLighIndex = 0;
            console.log("第一次加载该书,索引是:", this.highLighIndex);
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/reader/book/content.json?source_uuid=${this.$route.query.id}&content_uuid=${this.chapterId}`
              )
              .then((res) => {
                this.$nextTick(() => {
                  this.isShow = false;
                  this.chapterContent = "";
                  this.$refs.content.innerHTML = this.chapterContent;
                  this.chapterContent = res.data.data.content;
                  this.$refs.content.innerHTML += this.chapterContent;
                  this.isShow = true;
                  this.readHistory();
                  console.log(this.$refs.content);
                });
              });
          });
        });
    },

    // 点击上一章
    prevChapter() {
      // 高亮索引-1
      this.highLighIndex--;
      this.chapterId = this.chapterSecIdArray[this.highLighIndex];
      // 复用一下点击目录的方法
      this.loadCatalogChapter(
        this.highLighIndex,
        this.chapterId,
        this.bookList[this.highLighIndex].needPay
      );
    },
    // 点击下一章
    nextChapter() {
      // 高亮索引+1
      this.highLighIndex++;
      console.log("索引是:", this.highLighIndex); //==> 1
      // console.log("this.chapterSecIdArray是:", this.chapterSecIdArray); //空的
      this.chapterId = this.bookList[this.highLighIndex].secId;
      console.log("ID是:", this.chapterId);

      // 复用一下点击目录的方法
      this.loadCatalogChapter(
        this.highLighIndex,
        this.chapterId,
        this.bookList[this.highLighIndex].needPay
      );
    },

    // 收藏
    collect() {
      // json对象
      this.nullObject[this.$route.query.id] = {
        id: this.$route.query.id,
        type: "book",
      };

      // 获取本地书架的数据----以JSON对象的格式
      this.localShelf = JSON.parse(localStorage.getItem("localShelf") || "[]");
      if (this.localShelf.length > 0) {
        var idArray = [];
        this.localShelf.forEach((element) => {
          idArray.push(element.bookId);
        });
        if (idArray.indexOf(this.$route.query.id) != -1) {
          console.log("收藏过了");
        } else {
          console.log("收藏成功!");
          this.needCollect = false;
          // json对象格式
          this.localShelf.push(this.nullObject[this.$route.query.id]);
          // 把这个json对象存到本地-------以json字符串的形式
          localStorage.setItem("localShelf", JSON.stringify(this.localShelf));
          // 收藏成功就更新给vuex-------以json字符串的形式
          this.$store.dispatch("updateLocalBookShelf", this.localShelf);
        }
      } else {
        console.log("收藏成功!");
        this.needCollect = false;
        // json对象格式
        this.localShelf.push(this.nullObject[this.$route.query.id]);
        // 把这个json对象存到本地-------以json字符串的形式
        localStorage.setItem("localShelf", JSON.stringify(this.localShelf));
        // 收藏成功就更新给vuex-------以json字符串的形式
        this.$store.dispatch("updateLocalBookShelf", this.localShelf);
      }
    },
    // 阅读历史记录
    readHistory() {
      // 本地历史记录不需要push进去,因为没有排序的打算
      this.localHistory[this.$route.query.id] = {
        bookId: this.$route.query.id,
        secId: this.chapterId,
        time: new Date().getTime(),
      };
      localStorage.setItem("localHistory", JSON.stringify(this.localHistory));
    },
  },

  mounted() {
    // 赋值当前url链接
    this.product_url = document.URL;
    /*窗口自适应，关键代码*/
    window.onresize = () => {
      this.$nextTick(() => {
        this.num = 0;
        // window.location.reload();
      });
    };
    window.addEventListener("popstate", this.popstateFun, false);
  },

  destroyed() {
    //注销window.onresize事件
    window.onresize = null;
    window.removeEventListener("popstate", this.popstateFun);
  },
};
</script>

