<template>
  <!-- 书城 -->
  <!-- 采用了vant的van-tab标签页组件 并赋予route属性-->

  <div>
    <!-- 性别与搜索栏 -->
    <van-sticky>
      <div class="gender_search" style="background: white">
        <div class="gender_left" @click="ChangeGender">
          <span class="male" v-if="gender"
            ><i class="iconfont icon-82"></i
          ></span>
          <span class="female" v-else><i class="iconfont icon-nv"></i></span>
        </div>
        <div class="search_right" @click="EnterSearch">
          <input type="text" name="" id="" placeholder="请输入书名或作者" />
          <span class="search_icon"><i class="iconfont icon-sousuo"></i></span>
        </div>
      </div>
      <div class="bookmall_nav">
        <van-tabs
          :swipeable="true"
          v-model="active"
          route
          @change="session(active)"
        >
          <van-tab title="精选" to="/bookmall/selected"></van-tab>
          <van-tab title="免费" to="/bookmall/free"> </van-tab>
          <van-tab title="男生" to="/bookmall/male"> </van-tab>
          <van-tab title="女生" to="/bookmall/female"> </van-tab>
          <van-tab title="出版" to="/bookmall/publish"> </van-tab>
          <van-tab title="听书" to="/bookmall/listen"> </van-tab>
        </van-tabs></div
    ></van-sticky>
    <keep-alive> <router-view></router-view></keep-alive>
    <!-- <div v-for="(item, index) in 100" :key="index">
      <p>111</p>
    </div> -->
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      value: "",
      gender: true,
      active: 0,
    };
  },

  computed: {
    ...mapState(["genderNum"]),
    ...mapState(["bookmall_nav_index"]),
  },

  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      vm.$store.dispatch(
        "getBookmallNavIndex",
        Number(sessionStorage.getItem("bookmallValue")) || 0
      );
      vm.active = vm.bookmall_nav_index;
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    console.log("从书城页面离开");

    next();
  },
  methods: {
    goBack() {
      this.active = this.bookmall_nav_index;
    },
    // session
    session(i) {
      console.log(i);
      sessionStorage.setItem("bookmallValue", i);
    },
    ...mapActions(["changeGenderNum"]),
    // 改变性别,并修改性别值用来传参改变页面
    ChangeGender() {
      this.gender = !this.gender;
      this.$store.dispatch("changeGenderNum");
    },
    // 从书城进入搜索栏
    EnterSearch() {
      this.$router.push({
        name: "searchHint",
      });
    },
  },

  mounted() {
    // 监听页面会退事件
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", this.goBack, false);
    }
  },
  destroyed() {
    window.removeEventListener("popstate", this.goBack, false);
  },
};
</script>
