  display: flex;
  padding-left: 3vw;
  padding-top: 20px;

  .gender_left {
    background: rgb(236, 234, 234);
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    .male {
      i {
        font-size: 20px;
        color: rgb(95, 10, 206);
      }
    }
    .female {
      i {
        font-size: 20px;
        color: pink;
      }
    }
  }
  .search_right {
    position: relative;
    input {
      border: 1px solid gray;
      margin-left: 2vw;
      width: 81vw;
      height: 30px;
      border-radius: 18px;
      padding-left: 15px;
    }
    .search_icon {
      position: absolute;
      right: 15px;
      top: 4px;
      i {
        font-size: 24px;
      }
    }
  }
}
.bookmall_nav {
  // 书城上方导航栏的下滑横线
  .van-tabs__line {
    width: 15px;
    bottom: 23px;
