<template>
  <van-pull-refresh
    success-text="刷新成功!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!--轮播图组件 -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <van-swipe-item
          @click="enterBannerBookContent(item)"
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!-- 非常浅的下划线 -->
      <!-- <div class="underline"></div> -->

      <!-- 3书横向排序组件 -->
      <RowThree :row_three_book_sort="row_three_book_sort" />

      <!-- 3书竖列排序组件 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />
      <!-- 3书竖列排序组件 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort2" />
    </div>
    <BaseLine v-if="shortCut.length > 0" />
    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";

import ColumnThree from "../components/ColumnThree.vue";
import RowThree from "../components/RowThree.vue";
import BaseLine from "./BaseLine.vue";
export default {
  components: {
    ColumnThree,
    RowThree,
    BaseLine,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      // 横排三书数据
      row_three_book_sort: null,
      // 竖排三书数据
      column_three_book_sort: null,
      // 竖排第二份三书数据
      column_three_book_sort2: null,
    };
  },

  methods: {
    // 点击轮播图进入书籍
    enterBannerBookContent(book) {
      // 如果是正常书籍
      if (book.id) {
        this.$router.push({
          path: "/bookcontent",
          query: { id: book.id, homeKey: "/bookmall/free" },
        });
      } else {
        // 如果是剧本杀或者其他什么鬼东西
        this.$router.push({
          name: "jubensha",
          query: { url: book.url, homeKey: "/bookmall/free" },
        });
      }
    },
    // 下拉刷新重新发送请求
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;

            // 获取免费页面数据内容
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[1].url}`
              )
              .then((result) => {
                // 轮播图
                this.banner_cover = result.data.data.list[0].banners;
                // 获取横排三书排序的数据
                this.row_three_book_sort = result.data.data.list[1];
                // 获取竖排三书排序的数据
                this.column_three_book_sort = result.data.data.list[2];
                // 获取第二份竖排三书排序的数据
                this.column_three_book_sort2 = result.data.data.list[3];
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },
  beforeRouteEnter(to, from, next) {
    // ...
    to, from;
    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 1);
    });
  },
  beforeRouteLeave(to, from, next) {
    // 必须通过路由规则,离开该组件时被调用
    this.$store.dispatch("getBackHomeKey", from.path);

    next();
  },
  created() {
    //请求男生的数据
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 获取免费页面数据内容
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[1].url}`)
          .then((result) => {
            // 轮播图
            this.banner_cover = result.data.data.list[0].banners;
            // 获取横排三书排序的数据
            this.row_three_book_sort = result.data.data.list[1];
            // 获取竖排三书排序的数据
            this.column_three_book_sort = result.data.data.list[2];
            // 获取第二份竖排三书排序的数据
            this.column_three_book_sort2 = result.data.data.list[3];

            this.$toast.clear();
          });
      });
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  watch: {
    // 利用watch监听性别值的变化,一旦有所改变,就重新发送请求
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 自定义加载图标
      this.$toast.loading({
        message: "切换中...",
        forbidClick: true,
        loadingType: "circular",
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 获取免费页面数据内容
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[1].url}`)
            .then((result) => {
              // 轮播图
              this.banner_cover = result.data.data.list[0].banners;
              // 获取横排三书排序的数据
              this.row_three_book_sort = result.data.data.list[1];
              // 获取竖排三书排序的数据
              this.column_three_book_sort = result.data.data.list[2];
              // 获取第二份竖排三书排序的数据
              this.column_three_book_sort2 = result.data.data.list[3];
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// 解决底部导航栏遮住内容的div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// 建立一个div,生成非常非常浅的下划线,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
