<template>
  <!-- 听书频道 -->
  <van-pull-refresh
    success-text="刷新成功!"
    v-model="isLoading"
    :head-height="80"
    @refresh="onRefresh"
  >
    <div>
      <!--轮播图组件 -->
      <van-swipe
        v-if="banner_cover"
        class="my-swipe"
        :autoplay="3000"
        indicator-color="#1baeae"
      >
        <van-swipe-item
          @click="
            $router.push({
              path: '/bookcontent',
              query: { id: item.id, homeKey: '/bookmall/listen' },
            })
          "
          v-for="(item, index) in banner_cover"
          :key="index"
        >
          <img class="swipe-img" :src="item.cover" alt="" />
        </van-swipe-item>
      </van-swipe>

      <!-- 非常浅的下划线 -->
      <div class="underline"></div>
      <!-- 5书排序组件1 : 重磅推荐-->
      <FiveBook :five_book_sort="five_book_sort" />

      <!-- 3书竖列排序组件1: 本周热听 -->
      <ColumnThree :column_three_book_sort="column_three_book_sort" />
      <!-- 轮播横幅 -->

      <!-- 横向四书排序组件1 :女生精选 -->
      <RowFour :row_four_book_sort="row_four_book_sort" />
    </div>
    <BaseLine v-if="banner_cover.length > 0" />

    <div class="bottom_trim"></div>
  </van-pull-refresh>
</template>

<script>
import { mapState } from "vuex";
import FiveBook from "../components/FiveBook.vue";
import ColumnThree from "../components/ColumnThree.vue";

import BaseLine from "./BaseLine.vue";
import RowFour from "./RowFour.vue";
export default {
  components: {
    FiveBook,
    ColumnThree,

    BaseLine,
    RowFour,
  },
  data() {
    return {
      ctype: 1,
      // highly_recommended_week: {},
      information: [],
      banner_cover: [],
      shortCut: [],
      isLoading: false,
      //  5书排序组件1 : 重磅推荐
      five_book_sort: null,

      //  3书竖列排序组件1: 本周热听
      column_three_book_sort: null,
      //  轮播横幅

      //  横向四书排序组件1 :女生精选
      row_four_book_sort: null,
    };
  },

  methods: {
    // 下拉刷新重新发送请求
    onRefresh() {
      setTimeout(() => {
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
          )
          .then((res) => {
            // console.log(res);
            this.information = res.data.data.navi;
            console.log("selected-nav-information===>", this.information);
            // 获取听书页面数据内容
            this.axios
              .get(
                `https://apis.netstart.cn/yunyuedu/${this.information[5].url}`
              )
              .then((result) => {
                // 轮播图
                this.banner_cover = result.data.data.list[0].banners;

                //  5书排序组件1 : 重磅推荐
                this.five_book_sort = result.data.data.list[1];

                //  3书竖列排序组件1: 本周热听
                this.column_three_book_sort = result.data.data.list[2];
                //  轮播横幅

                //  横向四书排序组件1 :女生精选
                this.row_four_book_sort = result.data.data.list[4];
              });
          });
        this.isLoading = false;
      }, 1000);
    },
  },
  beforeRouteLeave(to, from, next) {
    // 必须通过路由规则,离开该组件时被调用

    this.$store.dispatch("getBackHomeKey", from.path);
    next();
  },
  beforeRouteEnter(to, from, next) {
    to, from;

    next((vm) => {
      vm.$store.dispatch("getBookmallNavIndex", 5);
      vm.$toast({
        message: "听书页面仅做了当前页面排版,请勿点击!.",
        duration: 3000,
      });
    });
  },

  created() {
    //请求男生的数据
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
      five_book_sort: null,
    });
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${this.genderNum}`
      )
      .then((res) => {
        // console.log(res);

        this.information = res.data.data.navi;
        console.log("selected-nav-information===>", this.information);
        // 获取听书页面数据内容
        this.axios
          .get(`https://apis.netstart.cn/yunyuedu/${this.information[5].url}`)
          .then((result) => {
            // 轮播图
            this.banner_cover = result.data.data.list[0].banners;

            //  5书排序组件1 : 重磅推荐
            this.five_book_sort = result.data.data.list[1];

            //  3书竖列排序组件1: 本周热听
            this.column_three_book_sort = result.data.data.list[2];
            //  轮播横幅

            //  横向四书排序组件1 :女生精选
            this.row_four_book_sort = result.data.data.list[4];

            this.$toast.clear();
          });
      });
  },
  computed: {
    ...mapState(["genderNum"]),
  },
  watch: {
    // 利用watch监听性别值的变化,一旦有所改变,就重新发送请求
    genderNum: function (newvalue, oldvalue) {
      console.log(newvalue, oldvalue);

      // 自定义加载图标
      this.$toast.loading({
        message: "切换中...",
        forbidClick: true,
        loadingType: "circular",
      });
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/store/navi.json?gender=${newvalue}`
        )
        .then((res) => {
          // console.log(res);
          this.information = res.data.data.navi;
          console.log("selected-nav-information===>", this.information);
          // 获取听书页面数据内容
          this.axios
            .get(`https://apis.netstart.cn/yunyuedu/${this.information[5].url}`)
            .then((result) => {
              // 轮播图
              this.banner_cover = result.data.data.list[0].banners;

              //  5书排序组件1 : 重磅推荐
              this.five_book_sort = result.data.data.list[1];

              //  3书竖列排序组件1: 本周热听
              this.column_three_book_sort = result.data.data.list[2];
              //  轮播横幅

              //  横向四书排序组件1 :女生精选
              this.row_four_book_sort = result.data.data.list[4];
            });
        });
    },
  },
};
</script>
<style lang="less" scoped>
// 解决底部导航栏遮住内容的div
.bottom_trim {
  content: "";
  height: 50px;
  display: block;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  // width: 100%;
  // height: 150px;
  text-align: center;
  /* background-color: #39a9ed; */
}

.swipe-img {
  // width: 100%;
  // height: 150px;
}
.ranking_nav {
  ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
    li {
      .yuan_color {
        width: 50px;
        height: 50px;
      }
    }
  }
}
// 建立一个div,生成非常非常浅的下划线,
.underline::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #fffafa;
  transform: scaleY(1);
}
</style>
