<template>
  <div>
    <!-- 占位栏 -->
    <div style="height: 86px"></div>
    <transition
      name="animate__animated animate__bounce"
      leave-active-class="animate__fadeOutUp"
    >
      <div
        v-show="zhanweiShow"
        :style="{ height: shaixuanHeight, background: 'red' }"
      ></div>
    </transition>

    <!-- 筛选栏 -->

    <div
      v-if="sortList.length > 0"
      style="
        text-align: left;
        position: fixed;
        top: 0;
        width: 100vw;
        background: white;
      "
    >
      <TitleBar :title="$route.query.title" />
      <!-- 第一条 -->
      <div
        style="
          display: flex;
          margin: 0 5%;
          position: relative;
          flex-wrap: wrap;
          height: 40px;
          line-height: 40px;
        "
      >
        <p
          @click="select(sortList[0].value, 'sort')"
          :class="{ activeCss: sortValue == sortList[0].value }"
          style="margin-right: 10px"
        >
          {{ sortList[0].name }}
        </p>
        <span style="margin-right: 10px">|</span>
        <p
          @click="select(sortList[1].value, 'sort')"
          :class="{ activeCss: sortValue == sortList[1].value }"
        >
          {{ sortList[1].name }}
        </p>
        <p
          style="text-align: right; position: absolute; right: 0"
          @click="toshow"
        >
          <span
            v-if="!shaixuan"
            style="
              border: 1px solid black;
              padding: 2px 10px 2px 10px;
              border-radius: 10px;
            "
          >
            ▼筛选</span
          >
          <span
            v-else
            style="
              border: 1px solid black;
              padding: 2px 10px 2px 10px;
              border-radius: 10px;
            "
          >
            ▲筛选</span
          >
        </p>
      </div>
      <transition
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeIn"
        leave-active-class="animate__fadeOutUp"
      >
        <!-- <span style="margin-right: 10px">·</span> -->
        <!-- 弹窗 -->
        <div v-show="shaixuan" style="margin: 0 5% 5px 5%" ref="tanchuang">
          <!-- 第二条 -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="pay in payList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="pay.value"
              :class="{ activeCss: payValue == pay.value }"
              @click="select(pay.value, 'pay')"
            >
              {{ pay.name }}
            </p>
          </div>
          <!-- 第三条 -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="bookStatus in bookStatusList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="bookStatus.value"
              :class="{ activeCss: bookStatusValue == bookStatus.value }"
              @click="select(bookStatus.value, 'bookStatus')"
            >
              {{ bookStatus.name }}
            </p>
          </div>
          <!-- 第四条 -->
          <div style="display: flex; flex-wrap: wrap">
            <p
              v-for="wordCount in wordCountList"
              style="margin-right: 10px; margin-bottom: 5px"
              :key="wordCount.value"
              :class="{ activeCss: wordCountValue == wordCount.value }"
              @click="select(wordCount.value, 'wordCount')"
            >
              {{ wordCount.name }}
            </p>
          </div>
        </div>
      </transition>
    </div>

    <van-list
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- 空状态:加载中 -->
      <van-empty v-if="nullStatus" description="加载中" />
      <!-- 空状态:匹配不到 -->
      <van-empty v-if="nullStatus2" description="没有找到你想要的内容" />
      <!-- 渲染的书籍列表 -->
      <van-row
        gutter="20"
        style="
          padding: 0 5%;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        "
      >
        <van-col
          span="8"
          v-for="(book, index) in bookList"
          :key="index"
          style="margin-bottom: 10px; width: 30%"
          @click="enterBookDetail(book.id)"
        >
          <img
            width="100%"
            :src="book.cover + '?imageView&thumbnail=240y336'"
            @error="errorImg"
          />

          <p
            style="
              text-align: left;
              font-size: 14px;
              margin-top: 10px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.title }}
          </p>
          <p
            style="
              text-align: left;
              font-size: 14px;
              color: gray;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.author }}
          </p>
        </van-col>

        <van-col span="8" style="margin-bottom: 10px; width: 30%"> </van-col>
      </van-row>
    </van-list>

    <div class="bottom_trim" style="margin-top: 50px"></div>
  </div>
</template>
<script>
import TitleBar from "../components/TitleBar.vue";
export default {
  components: { TitleBar },
  data() {
    return {
      shaixuan: false,
      zhanweiShow: false,
      shaixuanHeight: 0,
      top: "",
      // 列表
      loading: false,
      finished: false,
      // 筛选集合
      // 0全部 1收费 2免费 3包月
      payList: [],
      payValue: 0,
      //0全部 1连载 2完本
      bookStatusList: [],
      bookStatusValue: 0,
      // 0全部 130万字以下 230-50万字 350-100万字 4100万字以上
      wordCountList: [],
      wordCountValue: 0,
      // 1畅销 3最新
      sortList: [],
      sortValue: 1,
      categories: [],
      // 页码
      pageValue: 1,
      // 书籍列表
      bookList: [],
      // 空状态
      nullStatus: true,
      nullStatus2: false,
    };
  },
  // // // //
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      // 如果是从详情界面来的,则不更新
      if (from.path == "/bookdetail") {
        console.log("从书籍详情界面回来,不要更新");
      } else {
        // 异步更新数据
        vm.$axios
          .get(`https://apis.netstart.cn/yunyuedu${vm.$route.query.url}`)
          .then((res) => {
            res.data.categories;
            vm.$nextTick(() => {
              vm.categories = res.data.categories;
              vm.categories.forEach((element) => {
                if (element.type == "pay") {
                  vm.payList = element.catArray;
                } else if (element.type == "bookStatus") {
                  vm.bookStatusList = element.catArray;
                } else if (element.type == "wordCount") {
                  vm.wordCountList = element.catArray;
                } else if (element.type == "sort") {
                  vm.sortList = element.catArray;
                }
              });
              vm.onLoad();
            });
          });
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    if (to.path == "/bookmall/selected") {
      // 如果是要回退到书城,则初始化数据
      console.log(to, from);
      this.shaixuan = false;
      this.zhanweiShow = false;
      this.shaixuanHeight = 0;
      this.top = "";
      // 列表
      this.loading = false;
      this.finished = false;
      // 筛选集合
      // 0全部 1收费 2免费 3包月
      this.payList = [];
      this.payValue = 0;
      //0全部 1连载 2完本
      this.bookStatusList = [];
      this.bookStatusValue = 0;
      // 0全部 130万字以下 230-50万字 350-100万字 4100万字以上
      this.wordCountList = [];
      this.wordCountValue = 0;
      // 1畅销 3最新
      this.sortList = [];
      this.sortValue = 1;
      this.categories = [];
      // 页码
      this.pageValue = 1;
      // 书籍列表
      this.bookList = [];
      // 空状态
      this.nullStatus = true;
      this.nullStatus2 = false;
    } else {
      //
    }

    next();
  },
  methods: {
    // 进入书籍详情
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 设置筛选的时候
    select(selectValue, type) {
      console.log("要发送的筛选值:", selectValue);
      // 如果是第一栏点击的
      if (type == "sort") {
        this.sortValue = selectValue;
      } else if (type == "pay") {
        this.payValue = selectValue;
      } else if (type == "bookStatus") {
        this.bookStatusValue = selectValue;
      } else if (type == "wordCount") {
        this.wordCountValue = selectValue;
      }
      // 发送筛选后的数据请求
      this.finished = false;
      this.nullStatus = true;
      this.bookList = [];
      this.pageValue = 2;
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?${this.getCaption(
            this.$route.query.url
          )}&wordCount=${this.wordCountValue}&pay=${this.payValue}&bookStatus=${
            this.bookStatusValue
          }&sort=${this.sortValue}`
        )
        .then((res) => {
          this.$nextTick(() => {
            if (res.data.list) {
              this.nullStatus = false;
              this.bookList = [];
              this.pageValue = 2;
              this.bookList = res.data.list;
            } else {
              this.nullStatus = false;
              this.nullStatus2 = true;
            }
          });
        });
      // 如果点击的时候,屏幕滚动到了大于300的距离,切换时则要回到顶部
      if (this.top > 300) {
        this.$nextTick(() => {
          window.scrollTo(0, 0);
        });
      }
    },
    getCaption(str) {
      var index = str.lastIndexOf("?"); //获取?后边的字符串
      str = str.substring(index + 1, str.length);
      return str;
    },
    onLoad() {
      // 加载列表数据
      console.log(
        "地址:",
        this.$route.query.url,
        "字数:",
        this.wordCountValue,
        "收付费:",
        this.payValue,
        "完本连载:",
        this.bookStatusValue,
        "畅销最新:",
        this.sortValue,
        "页码:",
        this.pageValue
      );

      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?${this.getCaption(
            this.$route.query.url
          )}&wordCount=${this.wordCountValue}&pay=${this.payValue}&bookStatus=${
            this.bookStatusValue
          }&sort=${this.sortValue}&page=${this.pageValue}`
        )
        .then((res) => {
          if (res.data.list) {
            this.nullStatus = false;
            this.pageValue++;
            this.bookList.push(...res.data.list);
            // 加载状态结束
            this.loading = false;
          }

          if (res.data.next == null || res.data.next == undefined) {
            // 数据全部加载完成
            this.loading = false;
            this.finished = true;
          }
        });
    },
    toDetail() {
      console.log(1);
    },

    toshow() {
      // 当滚动条位于顶点的时候
      if (window.pageYOffset === 0) {
        // 当滚动条位于顶点,且弹窗关闭的时候
        if (this.shaixuan == false && window.pageYOffset === 0) {
          // 点击筛选---弹窗就要打开
          this.shaixuan = !this.shaixuan;
          this.$nextTick(() => {
            //当滚动条位于顶点,然后弹窗打开--占位栏要有弹窗高度值,并且显示为true,
            this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
            this.zhanweiShow = true;
          });
          // 当滚动条位于顶点,且弹窗打开的时候
        } else if (this.shaixuan == true && window.pageYOffset === 0) {
          //点击筛选,弹窗就要关闭
          this.shaixuan = !this.shaixuan;
          // 让占位栏高度不变,但是显示为false
          this.zhanweiShow = false;
        }
      }
      // 当滚动条不在顶部时
      if (window.pageYOffset > 0) {
        // 当滚动条不在顶部,且弹窗是关闭的时候
        if (this.shaixuan == false && window.pageYOffset > 0) {
          // 当滚动条不在顶部,且弹窗关闭,点击筛选,弹窗就要打开
          this.shaixuan = !this.shaixuan;
          // 当滚动条不在顶部,且弹窗关闭,点击筛选,弹窗就要打开,占位栏要有弹窗高度值,并且显示为true
          this.$nextTick(() => {
            this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
            this.zhanweiShow = true;

            // 然后屏幕也要往下滚动
            window.scrollBy(0, -this.$refs.tanchuang.offsetHeight);
          });
          // 当滚动条不在顶部,且弹窗处于打开的时候
        } else if (this.shaixuan == true && window.pageYOffset > 0) {
          // 点击筛选,弹窗就要关闭
          this.shaixuan = !this.shaixuan;
          //   当滚动条不在顶部,且弹窗处于打开,点击筛选,弹窗就得关闭,让占位栏高度不变,但是显示为false
          this.zhanweiShow = false;
          // 然后屏幕也要往上滚动
          setTimeout(() => {
            window.scrollBy(0, this.$refs.tanchuang.offsetHeight);
          }, 300);
        }
      }
    },
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
  },

  // 滚动监听
  mounted() {
    window.addEventListener("scroll", () => {
      this.top =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        window.pageYOffset;
    });
    window.onresize = () => {
      this.$nextTick(() => {
        this.shaixuanHeight = this.$refs.tanchuang.offsetHeight + "px";
        // window.location.reload();
      });
    };
  },

  destroyed() {
    //注销window.onresize事件
    window.onresize = null;
  },
  // 监听top值的变化
  watch: {
    // 屏幕滚动触发这个函数,top是距离,是滚动条顶端距离页面顶部的距离
    top(newValue, oldValue) {
      // 如果新值大于1,说明不在页面顶部的
      if (newValue > 1) {
        //   //新值大于旧值,这说明是滚动条向下,并且筛选栏打开的
        if (newValue > oldValue && this.shaixuan == true) {
          // 让弹窗和占位栏都缩回去,(不然我直接滚上去不就能看到占位栏了吗)
          this.shaixuan = !this.shaixuan;
          this.zhanweiShow = false;
        }
      }
    },
  },
};
</script>

<style>
.zhanwei {
  /*  */
}
.animate__animated,
.animate__bounce {
  --animate-duration: 0.4s;
}
.activeCss {
  color: red;
}
</style>