<template>
  <div class="book_detail_view">
    <div class="top">
      <van-col class="top_container" span="22" offset="1">
        <span @click="$router.back()"
          ><i class="iconfont icon-xiangzuojiantou"></i
        ></span>
        <span @click="shareShow"><i class="iconfont icon-share"></i></span>
      </van-col>
      <van-col span="1" style="height: 46px; line-height: 46px"> </van-col>
    </div>
    <input
      id="input_url"
      v-model="product_url"
      style="opacity: 0; position: absolute"
      type="text"
    />
    <!-- 分享面板弹窗 -->
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
    <!-- 顶部下划线 -->
    <van-row style="background: white"
      ><van-divider style="margin: 0 0 5px 0"
    /></van-row>
    <!-- 书籍详情 -->
    <div class="book_detail">
      <div class="detai_top" style="display: flex">
        <div style="width: 30%; padding-left: 4.16%; min-width: 123px">
          <img
            style="width: auto; height: auto; max-width: 100%; max-height: 100%"
            :src="cover + '?imageView&thumbnail=240y336'"
            alt=""
            @error="errorImg"
          />
        </div>
        <div style="text-align: left; padding-left: 4.16%">
          <p style="font-size: 20px; margin-bottom: 5px">{{ bookName }}</p>
          <p style="margin-bottom: 5px">
            <span style="font-size: 16px; color: gray; margin-right: 5px">{{
              bookChannel
            }}</span>
            <span style="font-size: 16px; color: gray; margin-right: 5px"
              >|</span
            >
            <span style="font-size: 16px; color: blue">{{ authorName }}</span>
          </p>
          <van-rate
            v-model="rateValue"
            readonly
            size="15"
            color="#ffd21e"
            void-icon="star"
            void-color="#eee"
            style="margin-bottom: 5px"
          />
          <p style="margin-bottom: 5px; font-size: 15px">
            <span style="color: gray; margin-right: 10px"
              >{{ totalWords }}字</span
            >
            <span style="color: gray">{{ totalClick }}点击</span>
          </p>
          <p v-if="price" style="color: gray; font-size: 15px">
            {{ price }}阅点/千字
          </p>
          <p v-else>
            <span v-if="totalPrice != 0" style="color: red"
              >{{ totalPrice }}阅点</span
            >
          </p>
        </div>
      </div>

      <div class="detail_bottom">
        <van-col span="22" offset="1">
          <p
            style="color: rgb(111 113 117); font-size: 14px"
            class="m-content overflow-line"
            id="J_description"
          >
            {{ introduce }}
            <br v-if="authorizer != undefined" />
            <br />
            <span v-if="authorizer != undefined">
              <span style="color: gray">授权方:</span>
              <span style="color: rgb(78 155 223)">{{ authorizer }}</span>
            </span>
            <br />
            <span v-if="workType != undefined" style="color: gray">
              <span>作品类型:</span>
              <span>{{ workType }}</span>
            </span>
          </p>

          <div
            class="btn-more"
            v-if="isShowMore"
            id="J_btnmore"
            @click="showmoreDesc($event)"
          >
            展开
          </div>
          <!-- 分割线 -->
          <van-divider style="margin-top: 24px; margin-bottom: 0" />
          <!-- 章节 -->
          <van-cell
            is-link
            style="padding-left: 0; padding-right: 0"
            @click="enterBookCatalog"
          >
            <div style="display: flex; justify-content: space-between">
              <div style="position: relative; width: 200px">
                <span>
                  <i style="font-size: 24px" class="iconfont icon-mulu"></i>
                </span>
                <span style="position: absolute; left: 29px; top: 0">
                  共{{ totalChapter }}章
                </span>
              </div>
            </div>
          </van-cell>
        </van-col>
      </div>

      <van-row class="collect_read">
        <van-col span="12">
          <p
            @click="collect"
            v-if="needCollect"
            style="padding: 20px 0"
            class="collect"
          >
            加入书架
          </p>
          <p
            @click="cancelCollect()"
            v-else
            style="padding: 20px 0; color: gray"
            class="collect"
          >
            移出书架
          </p>
        </van-col>
        <van-col span="12" @click="enterBookContent">
          <p
            style="padding: 20px 0; background: red; color: white"
            class="read"
          >
            免费试读
          </p>
        </van-col>
      </van-row>
      <div class="division"></div>
      <div class="division_trim"></div>
      <!-- 分割线 -->
      <van-divider style="margin-top: 0" />
      <div class="comment_top" style="position: relative">
        <div
          v-if="showAllComment.length > 5"
          style="
            position: absolute;
            right: 4.16%;
            bottom: 6px;
            z-index: 99;
            border: 1px solid gray;
            border-radius: 4px;
            padding: 5px;
          "
        >
          <div style="color: gray" @click="enterBookComment">查看全部评论</div>
        </div>
        <van-cell style="font-size: 16px" value="▌评论"> </van-cell>
      </div>
      <div class="comment">
        <Comment
          v-show="totalChapter > 0"
          :commentResult="commentResult"
          :FatherSkeletonShow="FatherSkeletonShow"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Comment from "../components/Comment.vue";
import { mapState } from "vuex";
export default {
  components: { Comment },
  data() {
    return {
      // 分享
      product_url: "",
      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
      isShowMore: false,
      isDescStatus: true,
      introduce: "",
      // 简介
      introduction: "",
      //评论
      commentResult: null,
      pageNum: 1,

      activatedId: null,
      FatherSkeletonShow: true,
      // 是否展现查看全部评论按钮
      showAllComment: [],
      // 书名
      bookName: "",
      // 书籍频道
      bookChannel: "",
      //作者
      authorName: "",
      //  星星等级
      rateValue: null,
      // 总字数
      totalWords: "",
      // 总点击
      totalClick: "",
      // 价格
      price: null,
      // 总订阅价格
      totalPrice: null,
      // 封面
      cover: "",
      // 版权
      authorizer: "",
      // 作品类型
      workType: "",
      // 总章节数量
      totalChapter: null,
      // 本地书架
      localShelf: [],
      nullObject: {},
      // 是否需要收藏进书架
      needCollect: true,
      // 暑假的书籍ID的合集
      shelfArray: [],
    };
  },
  computed: {
    ...mapState(["comment_num"]),

    ...mapState(["source_uuid"]),
  },
  methods: {
    // 拷贝链接
    autoCopyUrl() {
      const input = document.createElement("input");
      const url = this.$refs.invitUrl.innerText;
      document.body.appendChild(input);
      input.setAttribute("value", url);
      input.select();
      if (document.execCommand("copy")) {
        document.execCommand("copy");
        this.$message.success("复制分享链接成功");
      }
      document.body.removeChild(input);
    },
    popstateFun() {
      if (this.showShare == true) {
        this.showShare = false;
      }
    },
    // 点击分享
    shareShow() {
      this.showShare = true;
      this.moreShow = false;
      //让弹窗跳出来的时候就往浏览器跳转记录中添加一条空记录
      window.history.pushState(null, null, document.URL);
    },
    // 分享
    onSelect(option) {
      this.showShare = false;
      if (option.name == "复制链接") {
        var input = $("#input_url");
        input.select();
        document.execCommand("Copy");
        this.$toast("链接复制成功");
      } else {
        this.$toast("暂时仅支持复制链接");
      }
    },
    // 进入评论区
    enterBookComment() {
      this.$router.push({
        name: "bookcomment",
        query: { id: this.$route.query.id },
      });
    },
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },

    showmoreDesc(e) {
      let el = e.currentTarget;
      el.previousElementSibling.classList[
        !this.isDescStatus ? "add" : "remove"
      ]("overflow-line");
      el.classList[this.isDescStatus ? "add" : "remove"]("more-collapse");
      el.innerHTML = !this.isDescStatus ? "展开" : "收起";
      this.isDescStatus = !this.isDescStatus;
      this.isShowMore = true;
    },
    // 获取评论
    getComment(id) {
      return this.axios.get(
        `https://apis.netstart.cn/yunyuedu/comment/getComments.json?bookId=${id}&page=${this.pageNum}`
      );
    },
    // 获取书籍详情数据
    getBookDetail(id) {
      return this.axios.get(
        `https://apis.netstart.cn/yunyuedu/book/getsub.json?id=${id}`
      );
    },
    // 打开章节目录
    enterBookCatalog() {
      this.$router.push({
        name: "bookcatalog",
        query: { id: this.$route.query.id },
      });
    },
    // 进入内容页面
    enterBookContent() {
      this.$router.push({
        path: "/bookcontent",
        query: { id: this.$route.query.id },
      });
    },
    // 进入此页面时根据ID数据加载初始化
    initData(res) {
      this.$nextTick(() => {
        // 书名
        this.bookName = res.data.feed.entry.title;
        //  书籍频道
        this.bookChannel = res.data.feed.entry["pris:book"].category;
        // 作者
        this.authorName = res.data.feed.entry.author.name;
        // 星星等级
        this.rateValue = res.data.feed.entry["pris:ranking"].value * 1;
        //  总字数
        this.totalWords = res.data.feed.entry["pris:book"].words;
        this.totalWords = (this.totalWords / 10000).toFixed(1) + "万";
        //  总点击
        this.totalClick = res.data.feed.entry["pris:subscribe"].clicksCount;
        if (res.data.feed.entry["pris:book"].wprice !== undefined) {
          //  价格
          this.price = res.data.feed.entry["pris:book"].wprice * 1;
        }
        if (res.data.feed.entry["pris:book"].price) {
          // 总订阅价格
          this.totalPrice = res.data.feed.entry["pris:book"].price * 1;
        }
        //  封面
        this.cover = res.data.feed.entry.link[1].href;
        //  版权
        this.authorizer = res.data.feed.entry["pris:book"].authorizer;
        //  作品类型
        this.workType = res.data.feed.entry["pris:book"].paydesc;
        // 简介
        this.introduction = res.data.feed.entry.summary;
        // 总章节数量
        this.totalChapter =
          res.data.feed.entry["pris:book"].totalArticleCount[1];
      });
    },
    // 加入收藏书架
    collect() {
      // json对象
      this.nullObject[this.$route.query.id] = {
        id: this.$route.query.id,
        type: "book",
      };
      // 获取本地书架的数据----以JSON对象的格式
      this.localShelf = JSON.parse(localStorage.getItem("localShelf") || "[]");
      if (this.localShelf.length > 0) {
        var idArray = [];
        this.localShelf.forEach((element) => {
          idArray.push(element.bookId);
        });
        if (idArray.indexOf(this.$route.query.id) != -1) {
          console.log("收藏过了");
        } else {
          console.log("收藏成功!");
          this.needCollect = false;
          // json对象格式
          this.localShelf.push(this.nullObject[this.$route.query.id]);
          // 把这个json对象存到本地-------以json字符串的形式
          localStorage.setItem("localShelf", JSON.stringify(this.localShelf));
          // 收藏成功就更新给vuex-------以json字符串的形式
          this.$store.dispatch("updateLocalBookShelf", this.localShelf);
        }
      } else {
        console.log("收藏成功!");
        this.needCollect = false;
        // json对象格式
        this.localShelf.push(this.nullObject[this.$route.query.id]);
        // 把这个json对象存到本地-------以json字符串的形式
        localStorage.setItem("localShelf", JSON.stringify(this.localShelf));
        // 收藏成功就更新给vuex-------以json字符串的形式
        this.$store.dispatch("updateLocalBookShelf", this.localShelf);
      }
    },
    // 取消收藏
    cancelCollect(id) {
      id;
      this.localShelf = JSON.parse(localStorage.getItem("localShelf") || "[]");
      this.localShelf.some((item, i) => {
        // console.log(item, i);
        if (item.id === this.$route.query.id) {
          this.localShelf.splice(i, 1);
          return true;
        }
      });
      console.log("取消收藏成功!");
      this.needCollect = true;
      localStorage.setItem("localShelf", JSON.stringify(this.localShelf));
      this.$store.dispatch("updateLocalBookShelf", this.localShelf);
    },
  },
  created() {
    this.getBookDetail(this.$route.query.id).then((res) => {
      this.initData(res);
    });

    this.getComment(this.$route.query.id).then((res) => {
      this.showAllComment = res.data.all.list;
      res.data.all.next = null;
      res.data.all.list = res.data.all.list.slice(0, 5);
      this.commentResult = res;
      this.$store.dispatch("getCommentResult", this.commentResult);
    });
  }, // 激活时
  activated() {
    if (this.activatedId != this.$route.query.id && this.activatedId != null) {
      console.log(
        "this.activatedId != this.$route.query.id,开始更新新书的评论区"
      );

      this.$toast.loading({
        message: "新的评论加载中...",
        forbidClick: true,
        loadingType: "spinner",
        position: "bottom",
        duration: 0,
      });
      this.getBookDetail(this.$route.query.id).then((res) => {
        this.initData(res);
      });
      this.getComment(this.$route.query.id).then((res) => {
        this.showAllComment = res.data.all.list;
        res.data.all.next = null;
        res.data.all.list = res.data.all.list.slice(0, 5);
        this.commentResult = res;
        // 更新vuex的评论区数据
        this.$store.dispatch("getCommentResult", this.commentResult);
        this.$toast.clear();
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      window.scrollTo(0, 0);
      console.log("to=====>", to);
      console.log("from=====>", from);
      if (to.query.id == vm.source_uuid) {
        //  vuex的id初始化
        vm.$store.dispatch("getSourceUuId", null);
      }
      // 每本书进来之前,先看看有没有收藏在书架(遍历本地书架)
      vm.localShelf = JSON.parse(localStorage.getItem("localShelf") || "[]");
      // 遍历它
      if (vm.localShelf.length > 0) {
        console.log("vm.localShelf.length>0");
        vm.localShelf.forEach((item) => {
          vm.shelfArray.push(item.id);
        });
        vm.shelfArray.indexOf(to.query.id) !== -1
          ? (vm.needCollect = false)
          : (vm.needCollect = true);
      } else {
        vm.needCollect = true;
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    console.log("离开了书籍详情页面,把路由ID传给激活id;");
    console.log(to);
    console.log(from);
    // 把本次的route.id存储到activatedId,方便激活时做对比
    this.activatedId = from.query.id;
    // 交给VUEX
    this.$store.dispatch("getActivatedId", this.source_uuid);
    next();
  },

  // 失活时
  deactivated() {},
  watch: {
    introduction(newVal, oldVal) {
      oldVal;
      this.introduce = newVal;
      if (this.introduce.length > 0) {
        this.isShowMore = true;
      }
    },
  },
  mounted() {
    // 赋值当前url链接
    this.product_url = document.URL;
    window.addEventListener("popstate", this.popstateFun, false);
  },

  destroyed() {
    window.removeEventListener("popstate", this.popstateFun);
  },
};
</script>

<style lang="less" scoped>
[v-cloak] {
  display: none;
}
.book_detail_view {
  height: calc(100vh - 50px);

  .top {
    height: 46px;
    .top_container {
      display: flex;
      justify-content: space-between;
      height: 46px;
      line-height: 46px;
    }
  }
  .book_detail {
    .detail_bottom {
      text-align: left;
      margin-bottom: 20px;
      .m-content {
        &.overflow-line {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 3;
          overflow: hidden;
        }
      }

      .btn-more {
        z-index: 999;
        color: #fff;
        float: right;
        color: #5383e7;
        position: relative;
        margin-top: rc(5);
        padding-right: rc(33);

        &.more-collapse {
          &::after,
          &::before {
            top: 2px;
            transform: rotate(180deg);
          }

          &::before {
            top: 4px;
          }
        }

        &::after,
        &::before {
          width: 0;
          height: 0;
          content: "";
          position: absolute;
          right: 0;
          top: 7px;
          border: rc(12) solid transparent;
        }

        &::after {
          border-top-color: #5383e7;
          z-index: 1;
        }

        &::before {
          border-top-color: #1c2239;
          z-index: 2;
          top: 5px;
        }
      }
    }
  }
}
.division_trim {
  height: 10px;
}
.division {
  height: 10px;
  width: 100vw;
  background: #f5f5f5;
  position: absolute;
  left: 0;
}
</style>
