<template>
  <div style="position: relative">
    <div class="title_bar">
      <TitleBar :rightText="topRightText" :clickRight="backHome" />
    </div>
    <van-swipe class="my-swipe" indicator-color="white">
      <van-swipe-item>
        <div class="left">
          <!-- 头像 -->
          <div class="avatar" style="margin-top: 50px; margin-bottom: 10px">
            <van-image
              @click="
                openAvatar(
                  $route.query.userAvatar
                    ? $route.query.userAvatar
                    : require('@/assets/img/readyAvator.webp')
                )
              "
              round
              width="5rem"
              height="5rem"
              :src="
                $route.query.userAvatar
                  ? $route.query.userAvatar
                  : require('@/assets/img/readyAvator.webp')
              "
            />
          </div>

          <div class="nickName" style="margin-bottom: 10px">
            <span style="font-size: 16px">
              {{ $route.query.userNickName }}
            </span>
            <span
              v-if="gender == 0"
              style="color: blue"
              class="iconfont icon-82"
            ></span>
            <span
              v-else-if="gender == 1"
              style="color: pink"
              class="iconfont icon-nv"
            ></span>
          </div>
          <div
            @click="showMailPopup($route.query.userNickName)"
            class="mail"
            style="
              height: 30px;
              width: 100px;
              background: rgb(249 201 201 / 9%);
              margin: 0 auto;
            "
          >
            <p style="font-size: 16px; line-height: 30px">私信</p>
          </div>
        </div>
      </van-swipe-item>
      <van-swipe-item>
        <div class="right" style="text-align: left">
          <van-row
            class="peroson"
            style="margin-top: 100px; margin-bottom: 10px"
          >
            <van-col span="22" offset="1" style="font-size: 14px"
              >个人介绍</van-col
            >
          </van-row>
          <van-row class="introduction">
            <van-col span="22" offset="1" style="">
              <p
                v-if="intro !== Object"
                style="
                  background: rgba(128, 128, 128, 0.43);
                  padding: 5px 0 5px 4px;
                  font-size: 14px;
                "
              >
                {{ intro || "暂无简介" }}
              </p>
            </van-col>
          </van-row>
        </div>
      </van-swipe-item>
    </van-swipe>
    <div
      class="level"
      style="
        font-size: 12px;
        padding: 8px 0;
        position: absolute;
        color: white;
        display: flex;
        width: 100vw;
        top: 283px;
        justify-content: space-around;
        background: rgb(93 81 81 / 34%); ;
      "
    >
      <div>
        <span style="color: gray">等级 </span>
        <span>{{ $route.query.userLevel }}</span>
        <span> ({{ levelName }})</span>
      </div>
      <div>
        <span style="color: gray">经验值 </span>
        <span>{{ levelInfo }}</span>
      </div>
    </div>
    <van-popup
      :overlay="false"
      v-model="mailShow"
      position="right"
      :style="{ width: '100%', height: '100%' }"
    >
      <TitleBar :title="title" />
      <div>
        <p style="color: gray">暂无私信</p>

        <div
          style="
            width: 100%;
            background: #dadada;
            display: flex;
            padding: 10px 0;
            position: absolute;
            bottom: 0;
          "
        >
          <div style="margin-left: 5%; width: 70%; margin-right: 5%">
            <input
              autofocus="autofocus"
              type="text"
              placeholder="给TA发点什么吧~"
              style="width: 100%; padding-left: 10px; border: none"
            />
          </div>
          <div style="min-width: 15%; background: orange; border-radius: 13px">
            <p style="min-width: 56px">发送</p>
          </div>
        </div>
      </div>
    </van-popup>
    <div>以下内容,接口暂无数据</div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { ImagePreview } from "vant";
import TitleBar from "../components/TitleBar.vue";
export default {
  components: { TitleBar },
  computed: {
    ...mapState(["back_home_key"]),
  },

  data() {
    return {
      //传给私信弹窗的title
      title: null,
      // 控制私信弹窗
      mailShow: false,
      // 传给TitleBar组件的顶部右侧标题
      topRightText: "首页",
      // 个人简介:
      intro: null,
      // 经验值
      levelInfo: null,
      // 用户等级称号
      levelName: null,
      // 用来判断是否要重新请求的ID
      userInfoId: null,
      // 用户性别
      gender: 0,
    };
  },
  created() {
    this.getUserInfo(this.$route.query.userId).then((res) => {
      // 个人简介
      this.intro = res.data.userInfo.profileInfo.intro;
      // 经验值
      this.levelInfo = res.data.userInfo.levelInfo.info.common.points;
      // 用户等级称号
      this.levelName = res.data.userInfo.levelInfo.info.common.levelName;
      this.userInfoId = this.$route.query.userId;
    });
  },
  activated() {
    if (
      this.userInfoId != null &&
      this.userInfoId != this.$route.query.userId
    ) {
      console.log("激活!发现个人信息ID不一致,开始重新发送请求信息...");
      this.getUserInfo(this.$route.query.userId).then((res) => {
        // 个人简介
        this.intro = res.data.userInfo.profileInfo.intro;
        // 经验值
        this.levelInfo = res.data.userInfo.levelInfo.info.common.points;
        // 用户等级称号
        this.levelName = res.data.userInfo.levelInfo.info.common.levelName;
        this.userInfoId = this.$route.query.userId;
      });
    }
  },

  methods: {
    // 点击首页的方法,要传给子组件的
    backHome() {
      this.$router.push({
        path: this.back_home_key,
      });
    },
    // 控制私信弹窗
    showMailPopup(nickName) {
      this.mailShow = !this.mailShow;
      this.title = nickName;
      window.history.pushState(null, null, document.URL);
    },
    // 点击打开头像预览
    openAvatar(url) {
      ImagePreview({
        images: [url],
        showIndex: false,
        closeOnPopstate: true,
      });
    },
    //根据路由ID获取用户信息
    getUserInfo(userId) {
      //  把得到的queryId赋值给userInfoId,好等下次激活时做判断
      return this.axios.get(
        `https://apis.netstart.cn/yunyuedu/sns/user/getUserInfo.json?userId=${userId}`
      );
    },
  },
  mounted() {
    window.addEventListener("popstate", (e) => {
      if (this.mailShow == true) {
        e;
        this.mailShow = false;
      }
    });
  },
  destroyed() {
    window.removeEventListener("popstate", (e) => {
      if (this.mailShow == true) {
        e;
        this.mailShow = false;
      }
    });
  },
};
</script>

<style lang="less" scoped>
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  text-align: center;
  height: 270px;
}
.my-swipe {
  background: url(http://imgs.aixifan.com/o_1c7il0iijlh912mq74g1evu1hl62r.jpg)
    no-repeat fixed top;
  background-size: 100% 100%;
  .left {
  }
}
.custom-indicator {
  bottom: 40px;
}
</style>