<template>
  <!-- 书架--批量管理页面 -->
  <div>
    <div style="text-align: left; margin-left: 4.16%; position: relative">
      <!-- 全选 -->
      <van-button
        v-if="
          result.length !== bookShelfList.length || bookShelfList.length == 0
        "
        style="color: gray; border: none"
        @click="checkAll"
      >
        全选(共{{ result.length }} 本)
      </van-button>
      <!-- 取消全选 -->
      <van-button v-else @click="toggleAll" style="color: red; border: none">
        取消全选(共{{ result.length }} 本)
      </van-button>
      <div
        style="position: absolute; bottom: 13px; right: 34px"
        @click="backBookShelf()"
      >
        <p>完成</p>
      </div>
    </div>
    <van-empty
      v-if="!bookShelfList.length > 0"
      image="search"
      description="书架上没有可以管理的书籍哦"
    />
    <van-checkbox-group
      ref="checkboxGroup"
      v-model="result"
      v-if="bookShelfList.length > 0"
    >
      <!-- 复选的 -->
      <van-cell-group>
        <!-- 可复选 -->
        <van-cell
          v-for="(item, index) in bookShelfList"
          clickable
          :key="index"
          @click="toggle(index, item.id)"
        >
          <template #right-icon>
            <van-row style="min-width: 354px; width: 100vw; position: relative">
              <van-col span="5" offset="1">
                <div
                  style="min-width: 72px; min-height: 100px; text-align: left"
                >
                  <img
                    style="min-width: 72px; min-height: 100px"
                    :src="item.cover"
                    @error="errorImg"
                    alt=""
                    ref="img"
                  />
                </div>
              </van-col>
              <van-col
                span="13"
                offset="1"
                style="text-align: left; min-width: 180px"
              >
                <div>
                  <p
                    style="
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      font-size: 20px;
                    "
                  >
                    {{ item.title }}
                  </p>
                  <p
                    style="
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      position: absolute;
                      bottom: 40px;
                      color: gray;
                    "
                  >
                    {{ item.author }}
                  </p>
                  <p
                    style="
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 1;
                      overflow: hidden;
                      position: absolute;
                      bottom: 10px;
                      color: gray;
                    "
                  >
                    <span>最新章:</span> {{ item.updateTitle }}
                  </p>
                </div>
              </van-col>
              <van-col span="4" style="vertical-align: middle">
                <div style="margin-top: 45%">
                  <van-checkbox :name="item.id" ref="checkboxes" />
                </div>
              </van-col>
            </van-row>
          </template>
        </van-cell>
      </van-cell-group>
    </van-checkbox-group>
    <!-- 选择后出现的弹窗 -->
    <van-popup :value="selectShow" position="bottom" :overlay="false"
      ><van-divider />
      <p @click="confirmDelete">删除</p>
      <van-divider style="margin-bottom: 0" />
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { mapState } from "vuex";
export default {
  components: { [Dialog.Component.name]: Dialog.Component },

  data() {
    return {
      bookShelfList: [],
      result: [],
    };
  },
  computed: {
    ...mapState(["localBookShelfx"]),
    // 判断是否显示删除的弹窗
    selectShow: {
      get() {
        return this.result.length > 0 ? true : false;
      },
      set(v) {
        this.selectShow = v;
      },
    },
  },

  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      vm;
      var tempBookShelfList = [];
      tempBookShelfList = localStorage.getItem("localShelf") || [];
      vm.$store.dispatch(
        "updateLocalBookShelf",
        localStorage.getItem("localShelf")
      );
      // 如果他的长度大于0,也就是本地书架有书,就发生请求
      if (tempBookShelfList.length > 0) {
        vm.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/shelf/detail.json?ids=${tempBookShelfList}`
          )
          .then((res) => {
            vm.$nextTick(() => {
              vm.bookShelfList = res.data.data.list;
            });
          });
      } else {
        console.log("不请求");
      }
    });
  },
  methods: {
    //点击完成返回书架,并清空勾选的
    backBookShelf() {
      this.result = [];
      this.$router.back();
    },
    // 图片挂了的备用封面
    errorImg(event) {
      event;
      let img = this.$refs.img;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 确认是否删除
    confirmDelete() {
      Dialog.confirm({
        title: "移出书架",
        message: "是否将该选中的书籍移出书架?",
      })
        .then(() => {
          if (this.result.length == this.bookShelfList.length) {
            //  删除本地书架
            localStorage.removeItem("localShelf");
            this.bookShelfList = [];
            this.result = [];
            this.$store.dispatch(
              "updateLocalBookShelf",
              JSON.stringify(this.bookShelfList)
            );
          } else {
            this.result.forEach((element) => {
              this.bookShelfList.forEach((item, index) => {
                if (element === item.id) {
                  this.bookShelfList.splice(index, 1);
                }
              });
            });
            var newLocalBookShelfx = JSON.parse(this.localBookShelfx);
            this.result.forEach((element) => {
              newLocalBookShelfx.forEach((item, index) => {
                if (element === item.id) {
                  newLocalBookShelfx.splice(index, 1);
                }
              });
            });
            this.$store.dispatch(
              "updateLocalBookShelf",
              JSON.stringify(newLocalBookShelfx)
            );
            localStorage.setItem("localShelf", this.localBookShelfx);
            this.$toast("删除成功");
          }
        })
        .catch(() => {
          // on cancel
        });
    },
    toggle(index, bookId) {
      bookId;
      this.$refs.checkboxes[index].toggle();
    },
    checkAll() {
      this.$refs.checkboxGroup.toggleAll(true);
      // 若果result的长度等于bookshelflist的长度，则说明全选，则删除的时候直接清空本地书架
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
    },
  },
};
</script>

