const router = new VueRouter({
    routes
});
router.beforeEach((to, from, next) => {
    from;
    //如果是书城页面路径，就直接next()
    if (to.path !== '/bookmall') {
        next()
    } else {
        next(router.app.$options.store.state.back_home_key);

    }
});


export default router