<template>
  <!-- 搜索页面和搜索建议 -->
  <div class="search_view">
    <!-- 顶部搜索 -->
    <form action="/">
      <van-search
        class="top_search"
        v-model="keyword"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @cancel="onCancel"
        @input="debounce(keyword)"
      />
    </form>
    <!-- 热门搜索 -->
    <div class="search_view_child" v-if="isShowSearch">
      <div class="hot_search">
        <p>热门搜索</p>
      </div>
      <van-row class="hotwords_list">
        <van-col
          @click="enterBookDetail(item.id, item.name, 'iconfont icon-shu')"
          class="hotwords_item"
          span="12"
          v-for="(item, index) in hotWordsList"
          :key="index"
        >
          <div>
            <p :style="activation(index + 1)">{{ index + 1 }}</p>
          </div>
          <p class="hotword">{{ item.name }}</p>
        </van-col>
      </van-row>
      <!-- 历史搜索记录 -->
      <div class="lastest_search">
        <p>最近搜索</p>
        <span @click="clearHistory">
          <i class="iconfont icon-delete_rubbish"></i>
        </span>
        <div class="history_list" style="display: flex; flex-wrap: wrap">
          <!-- 历史记录布局 -->
          <div
            class="history_item"
            v-for="(item, index) in historyList"
            :key="index"
            @click="
              item.type == 'van-icon van-icon-search'
                ? enterSearchResult2(item.keyword, item.type)
                : enterBookDetail2(item.id, item.keyword, item.type)
            "
          >
            <i :class="item.type"></i>
            <!-- icon-shu -->
            {{ item.keyword }}
          </div>
        </div>
      </div>
    </div>
    <!-- 搜索建议 -->
    <div class="search_hint" v-if="isShowSearchHint">
      <div>
        <van-cell
          is-link
          class="search_keyword"
          @click="enterSearchResult(keyword, 'van-icon van-icon-search')"
        >
          <p>
            <span><i class="van-icon van-icon-search"></i></span>
            搜索"{{ keyword }}"
          </p>
        </van-cell>
        <van-cell
          is-link
          v-for="(item, index) in resultList"
          :key="index"
          @click="enterBookDetail(item.id, item.text, 'iconfont icon-shu')"
        >
          <div class="suggest_content">
            <div class="suggest_img" v-if="item.showDetail">
              <van-image width="70" height="92" fit="fill" :src="item.cover" />
            </div>
            <div class="right">
              <p>
                <span><i class="iconfont icon-shu"></i></span
                ><span v-html="item.text"></span>
              </p>
              <p v-if="item.showDetail"><span v-html="item.author"></span></p>
              <p v-if="item.showDetail">
                <span v-html="item.d"></span>
              </p>
            </div>
          </div>
        </van-cell>
      </div>
      <h2 v-if="isShowTip">没有搜索到匹配结果</h2>
    </div>
    <SearchResultView v-if="isShowResult" :keyword="keyword" />
  </div>
</template>
<style lang="less" scoped>
.van-search__content {
  border-radius: 32px;
}

.search_view {
  height: calc(100vh);
  background: #f7f8fa;
  .search_view_child {
    .hot_search {
      padding: 2rem 0 0.5rem 0.8rem;
      p {
        text-align: left;
        color: gray;
      }
    }
    .hotwords_list {
      .hotwords_item {
        display: flex;
        padding-left: 20px;
        height: 40px;
        line-height: 40px;
        margin-bottom: 5px;
        div {
          margin-top: 10px;
          margin-right: 10px;
          line-height: 20px;
          width: 20px;
          height: 20px;

          p {
            width: 20px;
            height: 20px;
            color: white;
            background: #e64c2c;
            border-radius: 50%;
          }
        }
        .hotword {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
          font-weight: bolder;
        }
      }
    }
    .lastest_search {
      padding: 1.8rem 0 0.5rem 0.8rem;
      position: relative;
      p {
        text-align: left;
        color: gray;
      }
      span {
        position: absolute;
        right: 23px;
        top: 28px;
        i {
          font-size: 20px;
        }
      }
      .history_list {
        .history_item {
          border: none;
          background: #f0f0f0;
          border-radius: 30px;
          padding: 3px 10px;
          font-size: 14px;
          margin: 10px 10px 0 10px;
          max-width: 70%;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 1;
          overflow: hidden;
        }
      }
    }
  }
  .search_hint {
    div {
      .search_keyword {
        p {
          padding-left: 10px;
          font-size: 16px;
          span {
            margin-right: 10px;
            i {
              font-size: 17px;
            }
          }
        }
      }
    }

    .van-cell {
      background: #f7f8fa;
      .suggest_content {
        display: flex;
        .suggest_img {
          width: 100px;
          height: 92px;
        }
        .right {
          padding-left: 10px;
          p:nth-child(1) {
            font-size: 16px;
          }
          p:nth-child(2) {
            color: #cfcfcd;
          }
          p:nth-child(3) {
            color: #cfcfcd;
          }
          p {
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            span {
              margin-right: 10px;
            }
          }
        }
      }
    }
  }
}
</style>
<script>
import SearchResultView from "./SearchResultView.vue";
export default {
  components: {
    SearchResultView,
  },
  data() {
    return {
      keyword: "",
      hotWordsList: [],
      searchWordList: [],
      resultList: [],
      // 搜索栏
      isShowSearch: true,
      // 搜索建议是否显示
      isShowSearchHint: false,
      // 搜索结果页面是否显示
      isShowResult: false,
      isShowTip: false,
      // 防抖定时器
      timer: null,
      // count: 0,
      // 历史搜索记录
      historyList: [],
    };
  },
  methods: {
    // 清除本地历史搜索记录功能
    clearHistory() {
      localStorage.removeItem("historyList");
      this.historyList = JSON.parse(localStorage.getItem("historyList")) || [];
    },
    // 第一次点击搜索关键字,进入搜索结果页面
    enterSearchResult(keyword, type) {
      if (keyword.trim() == "") {
        return;
      } else {
        this.isShowResult = true;
        this.isShowSearch = false;
        this.isShowSearchHint = false;
        this.pushHistory(keyword, type);
      }
    },
    // 点击历史记录的放大镜关键字,进入搜索结果页面
    enterSearchResult2(keyword, type) {
      if (keyword.trim() == "") {
        return;
      } else {
        this.keyword = keyword;
        this.isShowResult = true;
        this.isShowSearch = false;
        this.isShowSearchHint = false;
        this.pushHistory(keyword, type);
      }
    },
    // 点击热门搜索推荐的关键字或者给出搜索建议的关键字进入书籍详情界面
    enterBookDetail(bookId, keyword, type) {
      this.pushHistory(keyword, type, bookId);
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // 进入书籍详情界面
    enterBookDetail2(bookId, keyword, type) {
      this.pushHistory(keyword, type, bookId);
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },
    // push进历史记录
    pushHistory(keyword, type, bookId) {
      keyword = keyword.replace(/<\/font>/gi, "").replace(/<font[^>]+>/gi, "");

      // console.log({ keyword: keyword, type: type });
      // 判断是否有重复的
      let isRepeat = this.historyList.some((item) => {
        return item.keyword === keyword;
      });

      if (!isRepeat) {
        // 如果没有重复的，则添加到最前面
        this.historyList.unshift({ keyword: keyword, type: type, id: bookId });
        localStorage.setItem("historyList", JSON.stringify(this.historyList));
      } else {
        // 如果有重复的，则把重复的移除
        this.historyList = this.historyList.filter((item) => {
          return item.keyword !== keyword;
        });
        // 再添加到最前面
        this.historyList.unshift({ keyword: keyword, type: type, id: bookId });
        localStorage.setItem("historyList", JSON.stringify(this.historyList));
      }
      // 如果超过10条，则删除最后一条
      if (this.historyList.length > 10) {
        this.historyList.pop();
        localStorage.setItem("historyList", JSON.stringify(this.historyList));
      }
    },

    // 输入框防抖
    debounce(keyword) {
      this.isShowResult = false;
      this.isShowTip = false;
      this.isShowSearch = false;
      this.isShowSearchHint = true;
      console.log("用户在等待时间又在输入框修改了");
      clearTimeout(this.timer);
      if (this.keyword == "") {
        // this.$toast("请输入搜索内容");
        // 关键字为空的时候让搜索建议页面隐藏并且搜索建议列表设置为空
        this.resultList = [];
        this.isShowSearch = true;
        this.isShowSearchHint = false;
        return;
      }
      this.timer = setTimeout(() => {
        console.log("用户暂停输入了,则发送axios请求开始搜索关键词");
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          loadingType: "spinner",
          duration: 0,
        });
        this.axios
          .get(
            `https://apis.netstart.cn/yunyuedu/search/searchHint?search=${keyword}`
          )
          .then((res) => {
            this.searchWordList = res.data.words.w;
            this.$toast.clear();
          })
          .then(() => {
            this.resultList = [];

            if (
              this.searchWordList !== undefined &&
              $.isArray(this.searchWordList) == true
            ) {
              this.searchWordList.forEach((item) => {
                if (
                  item.text.indexOf(this.keyword) > -1 ||
                  item.author.indexOf(this.keyword) > -1 ||
                  item.d.indexOf(this.keyword) > -1
                ) {
                  this.resultList.push(item);
                }
              });
            } else if (this.searchWordList === undefined) {
              console.log("抱歉,没有任何搜索结果");
            } else {
              if (
                this.searchWordList.text.indexOf(this.keyword) > -1 ||
                this.searchWordList.author.indexOf(this.keyword) > -1 ||
                this.searchWordList.d.indexOf(this.keyword) > -1
              ) {
                this.resultList.push(this.searchWordList);
              }
            }

            if (this.resultList.length == 0) {
              this.isShowTip = true;
            }

            this.resultList.map((item) => {
              item.text = this.brightKeyword(item.text);
              item.author = this.brightKeyword(item.author);
              item.d = this.brightKeyword(item.d);
            });
          });
      }, 1200);
    },
    brightKeyword(val) {
      let keyword = this.keyword;
      if (val.indexOf(keyword) !== -1) {
        return val.replace(keyword, `<font color='#42cccc'>${keyword}</font>`);
      } else {
        return val;
      }
    },
    // 确认搜索
    onSearch(val) {
      val;
      console.log("val", val);
    },
    // 返回上一页
    onCancel() {
      this.$router.back();
    },
  },

  computed: {
    activation() {
      return (index) => {
        if (index === 1) {
          return { background: "#e74c2c" };
        } else if (index === 2) {
          return { background: "#fe7817" };
        } else if (index === 3) {
          return { background: "#ffbf01" };
        } else {
          return { background: "#d9d9d9" };
        }
      };
    },
  },

  mounted() {},
  beforeRouteEnter(to, from, next) {
    to, from;
    // console.log("to===>", to);
    // console.log("from===>", from);
    next((vm) => {
      // 每次进来先从本地历史记录创建历史记录列表
      vm.historyList = JSON.parse(localStorage.getItem("historyList")) || [];
      vm;
      //如果来自页面是详情页,则不改变,否则清空关键字
      if (from.path == "/bookdetail") {
        vm.keyword;
      } else {
        vm.keyword = "";
      }
      vm.axios
        .get("https://apis.netstart.cn/yunyuedu/search/getHotWords")
        .then((res) => {
          vm.hotWordsList = res.data.list;
        });
    });
  },
};
</script>

